<template>
  <div>
    <div class="header" ref="indexHeader">
      <div class="top">
        <div class="big-title">软件小站</div>
        <div class="small-title">
          <div>{{ homeText }}</div>
          <div class="insert">|</div>
        </div>
        <div class="top-buttons">
          <button class="raise">前端</button>
          <button class="raise">后端</button>
          <button class="raise">算法</button>
          <button class="raise">其它</button>
        </div>
        <div class="more">
          <div class="left"><div id="sakana-a"></div></div>
          <a href="#center" @click.prevent="scrollToTarget">
            <div class="more-center">
              <div class="title">开始阅读</div>
              <div><i class="el-icon-arrow-down"></i></div>
            </div>
          </a>
          <div class="right"><div id="sakana-b"></div></div>
        </div>
      </div>
    </div>
    <div class="indexCenter" ref="indexCenter">
      <div class="left">
        <div v-if="articles.length != 0">
          <div
            class="item"
            @click="ToBlogDetail(item)"
            v-animate
            v-for="item in articles"
            :key="item.uuid"
          >
            <div class="left-left">
              <div class="left-bigTitle">
                {{ item.title }}
              </div>
              <div class="left-classify">
                <i class="el-icon-folder-opened"></i> {{ item.classify }}
              </div>
              <div class="left-subTitle">
                {{ item.introduce }}
              </div>
              <div class="left-bottom">
                <div class="left-bottom-time">
                  <i class="el-icon-date"></i> {{ item.time | formdata }}
                </div>
                <div class="left-bottom-look">
                  <i class="el-icon-view"></i> {{ item.love }}
                </div>
              </div>
            </div>
            <div class="left-right">
              <el-image
                :src="'https://8.130.43.227/images/' + item.img"
                :preview-src-list="['https://8.130.43.227/images/' + item.img]"
                v-if="item.img != ''"
                @click.stop
              >
                <div
                  slot="error"
                  style="
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    height: 100%;
                  "
                  class="image-slot"
                >
                  <i class="el-icon-picture-outline"></i>
                </div>
              </el-image>
              <el-image
                @click.stop
                v-else
                :src="blogImage"
                :preview-src-list="[blogImage]"
              >
                <div
                  slot="error"
                  style="
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    height: 100%;
                  "
                  class="image-slot"
                >
                  <i class="el-icon-picture-outline"></i>
                </div>
              </el-image>
            </div>
          </div>
          <div class="indexCenter-left-bottom">
            <el-pagination
              background=""
              :total="total"
              size="small"
              @current-change="handleCurrentChange"
              v-show="total >= 5"
            ></el-pagination>
          </div>
        </div>
        <div style="text-align: center" v-else>
          <el-card>{{ showBlogLoadingText }}</el-card>
        </div>
      </div>
      <div class="right">
        <el-card class="right-user" shadow="always">
          <div class="top">
            <img
              @click="OpenMyImage()"
              v-if="userEmailImageUrl != null"
              :src="userEmailImageUrl"
              style="cursor: pointer"
            />
            <img v-else src="@/assets/index/user.jpg" />
            <el-image
              id="userEmailImageUrl"
              style="display: none"
              v-if="userEmailImageUrl != null"
              :src="userEmailImageUrl"
              :preview-src-list="[userEmailImageUrl]"
            ></el-image>
          </div>
          <div class="center">
            <div class="center-user">{{ userInfo[0] }}</div>
            <div class="center-motto">它在我的机器上可以很好运行！</div>
          </div>
          <div class="footer">
            <div class="footer-header">
              <div class="footer-header-major">专业：{{ userInfo[1] }}</div>
              <div class="footer-header-classify">
                分类：
                <div
                  class="footer-header-classify-item"
                  v-for="item in classifyList"
                  :key="item"
                >
                  {{ item }}
                </div>
              </div>
              <div class="footer-header-article">
                发表文章数量：{{ userInfo[2] }}
              </div>
            </div>
            <div class="footer-footer">
              <div
                @click="OpenTelegram()"
                title="Telegram"
                class="footer-telegram"
              >
                <i class="el-icon-s-promotion"></i>
              </div>
              <div
                title="Share"
                @click="ShareAssociation()"
                class="footer-share"
              >
                <i class="el-icon-share"></i>
              </div>
              <div
                title="Refresh"
                @click="ReloadIndexHome()"
                class="footer-Refresh"
              >
                <i class="el-icon-refresh"></i>
              </div>
            </div>
          </div>
        </el-card>
        <IndexAboutMeVue></IndexAboutMeVue>
      </div>
    </div>
  </div>
</template>

<script>
import "sakana-widget/lib/index.css";
import SakanaWidget from "sakana-widget";
import IndexAboutMeVue from "./IndexAboutMe.vue";
export default {
  components: { IndexAboutMeVue },
  data() {
    return {
      skip: 1, //分页数据
      classifyList: ["前端", "后端", "算法", "其它"], //标签数据
      articles: [], //博客数据
      categoryName: "", //标签（分类）名字
      total: 0, //博客条数
      userSearch: "", //用户搜索内容
      blogImage: require("@/assets/image/backimg.jpg"), //博客数据
      userInfo: [], //用户数据
      userEmailImageUrl: null, //用户图片
      showBlogLoadingText: "博客内容正在加载", //未显示的时候博客数据
      homeText: "", //主页展示数据
    };
  },
  filters: {
    formdata(timetemp) {
      const now = new Date(+timetemp).toLocaleString();
      return now;
    },
  },
  mounted() {
    //打印文字
    this.priText();
    //GitHub上面的小人
    this.github();
    //调用打字机
    this.printText("思维交织，灵感迸发");
    //监听用户刷新动作回到顶部
    window.addEventListener("beforeunload", this.beforeUnload);
    //发起请求获取主页面博客初始数据
    this.GetBlogAll();
    //博客搜索数据的搜索
    this.IndexHomeUserSearch();
    //用户获取自己的文章数量和自己的详情
    this.GetBlogUserCount();
  },
  methods: {
    //打印文字
    async priText() {
      let addText = "...";
      for (let i = 0; i < addText.length; i++) {
        await this.sleep(500);
        this.showBlogLoadingText += addText[i];
      }
      await this.sleep(500);
      this.showBlogLoadingText = "博客内容正在加载";
      this.priText();
    },
    //打开自己的图片
    OpenMyImage() {
      document.getElementById("userEmailImageUrl").click();
    },
    //翻页数据
    handleCurrentChange(skip) {
      this.skip = skip;
      this.GetBlogAll();
    },
    //处理用户获取自己的文章数量和自己的详情
    GetBlogUserCount() {
      const id = localStorage.getItem("id");
      this.$axios.get("/Blog/GetBlogUserCount?id=" + id).then((res) => {
        if (res.data.code == 200) {
          this.userInfo = res.data.data;
          this.userEmailImageUrl = this.userInfo[3];
          if (
            this.userEmailImageUrl != null &&
            this.userEmailImageUrl.endsWith("@qq.com")
          ) {
            this.userEmailImageUrl = this.userEmailImageUrl.slice(
              0,
              this.userEmailImageUrl.length - 7
            );
            this.userEmailImageUrl = `http://q2.qlogo.cn/headimg_dl?dst_uin=${this.userEmailImageUrl}&spec=100`;
          }
        }
      });
    },
    //跳转博客详情，并带上内容
    ToBlogDetail(e) {
      //设置颜色为透明色
      this.$bus.$emit("SetIndexTopBackgroundColor", 0);
      this.$axios.get("/Blog/BlogDetailAdd?id=" + e.uuid);
      //更换核心内容数据
      this.$router.push({
        path: "/index/blog",
        query: {
          detail: e.uuid,
          title: e.title,
          classify: e.classify,
          introduce: e.introduce,
          time: e.time,
          look: e.love,
          img: e.img,
        },
      });
    },
    //博客搜索数据
    IndexHomeUserSearch() {
      this.$bus.$on("IndexHomeUserSearch", (search) => {
        this.userSearch = search;
        this.scrollToTarget();
        this.GetBlogAll();
      });
    },
    //博客第一页数据
    GetBlogAll() {
      this.$axios
        .get(
          `/Blog/GetBlogAll?skip=${this.skip}&search=${this.userSearch}&categoryName=${this.categoryName}`
        )
        .then((res) => {
          if (res.data.code == 200) {
            this.articles = res.data.data.records;
            this.total = res.data.data.total;
          }
        });
    },
    //打开telegram网站
    OpenTelegram() {
      window.open("https://web.telegram.org/");
    },
    //分享网站
    ShareAssociation() {
      if (navigator) {
        this.$notify.success({
          message: "This website URL has been successfully copied",
        });
        navigator.clipboard.writeText("https://8.130.43.227");
      }
    },
    //分页插件点击
    onClickHandler() {},
    //用户刷新动作回到顶部
    beforeUnload() {
      setTimeout(() => {
        window.scrollTo({
          top: 0,
          behavior: "auto", // 不能保证平滑滚动，因为在 beforeunload 中有限制
        });
      }, 0);
    },
    //刷新页面动作
    ReloadIndexHome() {
      this.$notify.error({ message: "Refresh" });
      window.scrollTo({
        top: 0,
        behavior: "smooth",
      });
      setTimeout(() => {
        location.reload();
      }, 1000);
    },
    //阅读更多的动画
    scrollToTarget() {
      let indexCenter = this.$refs.indexCenter;
      indexCenter.scrollIntoView({ behavior: "smooth" });
    },
    //打字机调用打印
    async printText(text) {
      for (let i = 0; i < text.length; i++) {
        await this.sleep(200); // 控制打印速度，这里是每200毫秒打印一个字符
        this.homeText += text[i];
      }
      setTimeout(async () => {
        for (let i = text.length - 1; i >= 0; i--) {
          // 从后往前删除字符
          await this.sleep(200); // 控制打印速度，这里是每200毫秒删除一个字符
          this.homeText = this.homeText.slice(0, -1); // 删除最后一个字符
        }
        let welcome = "欢迎来到软件小站呀";
        for (let i = 0; i < welcome.length; i++) {
          await this.sleep(200); // 控制打印速度，这里是每200毫秒打印一个字符
          this.homeText += welcome[i];
        }
        setTimeout(async () => {
          for (let i = welcome.length - 1; i >= 0; i--) {
            // 从后往前删除字符
            await this.sleep(200); // 控制打印速度，这里是每200毫秒删除一个字符
            this.homeText = this.homeText.slice(0, -1); // 删除最后一个字符
          }
        }, 2000);
        setTimeout(() => {
          this.homeText = "";
          this.printText("思维交织，灵感迸发");
        }, 6000);
      }, 2000); // 在打印完成后延迟2秒执行删除操作
    },
    sleep(ms) {
      return new Promise((resolve) => setTimeout(resolve, ms));
    },
    //更改GitHub的弹弹弹
    github() {
      const takina = SakanaWidget.getCharacter("takina");
      takina.initialState = {
        ...takina.initialState,
        i: 0.001,
        d: 1,
      };
      SakanaWidget.registerCharacter("takina-slow", takina);
      new SakanaWidget().mount("#sakana-a");
      const github = SakanaWidget.getCharacter("chisato");
      github.image = require("@/assets/index/takina.png");
      SakanaWidget.registerCharacter("github", github);
      new SakanaWidget({ character: "github" }).mount("#sakana-b");
    },
  },
};
</script>

<style lang="less" scoped>
.header {
  width: 100%;
  height: 100vh;
  background-size: 100% 100%;
  background-image: url("@/assets/index/indexBack.jpg");
  position: relative;
  z-index: 0;
  overflow: hidden;
  .top {
    color: white;
    .big-title {
      text-align: center;
      font-size: 4vw;
      text-shadow: #fc0 1px 0 10px;
      padding-top: 10vh;
    }
    .small-title {
      margin-top: 2vh;
      font-size: 1vw;
      display: flex;
      justify-content: center;
      .insert {
        animation: insertAni 1s ease-in-out infinite;
      }
      @keyframes insertAni {
        from {
          opacity: 0;
        }
        to {
          opacity: 1;
        }
      }
    }
    .top-buttons {
      display: flex;
      justify-content: center;
      margin-top: 25vh;
      button {
        transition: 0.25s;
        cursor: pointer;
        text-shadow: rgb(0, 0, 0) 1px 0 10px;
        background: none;
        font: inherit;
        line-height: 1;
        margin: 0.5em;
        padding: 1em 2em;
        color: white;
        border: none;
      }

      .raise:hover,
      .raise:focus {
        transform: translateY(-0.25em);
        border: 0.15vw solid white;
      }
      .raise {
        box-shadow: 0 0.5em 0.5em -0.4em #ffa260;
      }
    }
    .more {
      display: flex;
      justify-content: space-around;
      .more-center {
        text-align: center;
        margin-top: 20vh;
        font-size: 0.8vw;
        cursor: pointer;
        .title {
          animation: titleMove 2s ease-in-out infinite;
        }
        @keyframes titleMove {
          0% {
            opacity: 0.4;
            transform: translateY(-1vh);
          }
          30% {
            opacity: 0.7;
            transform: translateY(0);
          }
          100% {
            opacity: 0.5;
            transform: translateY(-1vh);
          }
        }
        i {
          font-size: 2vw;
          animation: titleMove 2s ease-in-out infinite;
        }
      }
    }
  }
  a {
    color: white;
    text-decoration: none;
  }
}

.indexCenter {
  padding-top: 10vh;
  display: flex;
  justify-content: center;
  background-color: #f5f5f5;
  padding-bottom: 5vh;

  .left,
  .right {
    align-self: flex-start; /* 确保每个部分独立对齐 */
  }
  .left {
    width: 55vw;
    .item {
      display: flex;
      margin-top: 3vh;
      width: 55vw;
      border-radius: 10px;
      background-color: #ffffff;
      line-height: 6vh;
      height: 33vh;
      overflow: auto;
      position: relative;
      &:hover {
        .el-image {
          transform: scale(1.1);
        }
        .left-bigTitle::after {
          width: 100%;
          left: 0;
        }
      }
    }
    .indexCenter-left-bottom {
      display: flex;
      justify-content: center;
      padding: 10vh;
    }

    .left-left {
      cursor: pointer;
      width: 35vw;
    }
    .left-bigTitle {
      padding-top: 1vh;
      padding-left: 2vw;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      font-size: 1.5vw;
      color: #4b41cc;
      position: relative;
    }
    .left-bigTitle::after {
      display: block;
      width: 0;
      height: 1px;
      background-color: #928cee;
      content: "";
      position: absolute;
      bottom: 0;
      right: 0%;
      transition: 1s;
    }

    .left-classify {
      margin-left: 2vw;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      opacity: 0.5;
      font-size: 1vw;
      position: relative;
    }
    .left-subTitle {
      padding-left: 2vw;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      opacity: 0.8;
      font-size: 1vw;
    }
    .left-bottom {
      display: flex;
      justify-content: space-between;
      font-size: 0.7vw;
      .left-bottom-time {
        opacity: 0.6;
        position: relative;
        margin-left: 2vw;
      }
      .left-bottom-look {
        transition: 0.25s;
        opacity: 0.6;
        &:hover {
          transform: translateY(-0.25em);
        }
      }
    }
    .left-right {
      overflow: hidden;
      margin-left: 1vw;
      width: 19vw;
      height: auto;

      .el-image {
        width: 100%;
        border-radius: 0px 10px 10px 0px;
        height: 100%;
        transition: transform 1s ease; /* 过渡效果 */
      }
    }
  }
  .right {
    margin-left: 1vw;
    width: 20vw;
    .right-user {
      border-radius: 10px;
      .top {
        display: flex;
        justify-content: center;
        img {
          width: 50%;
          border-radius: 50%;
        }
      }
      .center {
        text-align: center;
        padding: 2vh;
        .center-user {
          padding: 2vh;
          font-size: 2vw;
        }
        .center-motto {
          font-size: 1vw;
        }
      }
      .footer {
        .footer-header {
          font-size: 1vw;
          .footer-header-major {
            padding: 2vh;
          }
          .footer-header-classify {
            padding: 2vh;
            display: flex;
            justify-content: space-around;
            .footer-header-classify-item {
              transition: 1s;
            }
            .footer-header-classify-item:hover {
              transform: translateY(-0.25em);
              color: #4b41cc;
              cursor: pointer;
            }
          }
          .footer-header-article {
            padding: 2vh;
          }
        }
        .footer-footer {
          margin-top: 2vh;
          display: flex;
          justify-content: space-evenly;
          .footer-telegram {
            cursor: pointer;
          }
          .footer-share {
            cursor: pointer;
          }
          .footer-Refresh {
            cursor: pointer;
          }
        }
      }
    }
  }
}
</style>