<template>
  <div id="back">
    <div class="header" ref="indexHeader">
      <div>
        <img src="@/assets/words/back.png" alt="" />
      </div>
      <div class="title">
        <span @click="Refresh()">悠悠小语</span>
      </div>
      <div class="header-bottom">
        <div class="time">每个黎明都是一份新的礼物，送给你的。</div>
      </div>
    </div>
    <div class="center">
      <div class="left">
        <el-card class="left-introduce">
          <!-- 介绍版 -->
          <div>
            <div ref="LeaveWordsHello" class="introduce-title">
              Hello！亲爱的访客！👋
            </div>
            <div class="introduce-introduce">
              当你踏入这个留言板的小天地，我想要用一份热烈的欢迎迎接你的到来！在这里，我们期待着聆听你的声音、分享你的心情。无论是心情随笔、想法碎碎念，还是对生活的点滴感悟，都能在这里找到志同道合的朋友。所以，请毫不犹豫地留下你的足迹，与我们一同构建这个充满温馨和交流的小社区。Hi，欢迎加入我们，期待与你一起创造更多美好的留言板瞬间！！
            </div>
            <div class="introduce-introduce">
              <span style="font-weight: 600">前端、后端、算法、数据结构</span
              >都有涉猎，兴趣比较广泛。
            </div>
            <div ref="LeaveWordsIntorduce" class="leave-words-title">
              「心墙留香，码梦成篇」🤔
            </div>
            <div class="leave-words-why">
              - 在这里，我们用心与技术编织交流的思绪，分享彼此的点滴心情。
            </div>
            <div ref="LeaveWordsWhy" class="leave-words-title">
              「如何留言？」 📝
            </div>
            <div class="leave-words-why">
              <div>在下方的留言框中输入您的留言内容即可。</div>
              <div>
                请注意，为了保持良好的阅读体验和交流氛围，禁止发布任何不良信息和言论。
              </div>
            </div>
            <div ref="LeaveWordsMoney" class="leave-words-title">「赞助」</div>
            <div class="leave-words-why">
              如果您觉得小站的内容对您有所帮助，欢迎赞助小站，以支持小站继续创作更好的内容。🤗
            </div>
            <div class="leave-words-image">
              <el-image
                :src="require('@/assets/index/qrcode.png')"
                :preview-src-list="[require('@/assets/index/qrcode.png')]"
              ></el-image>
            </div>
            <div ref="LeaveWordsEmail" class="leave-words-title">
              「联系方式」📫
            </div>
            <a href="mailto:2450911274@protonmail.com">
              <div class="leave-words-email">
                <img src="@/assets/words/mail_green.svg" alt="" />
                <div class="leave-words-email-box">
                  <div class="email-title">Email</div>
                  <div class="email">
                    <span>2450911247@protonmail.com</span>
                  </div>
                  <div class="email"><span>yyy330612@gmail.com</span></div>
                </div>
              </div>
            </a>
            <div
              class="leave-words-title"
              style="color: #4f46e5"
              ref="LeaveWordsThank"
            >
              感谢您的支持！ 🙏
            </div>
            <div class="leave-words-why">
              非常感谢您来到小站的留言板，并留下您宝贵的意见和建议。小站会竭尽全力，为大家提供更好的服务和内容！
            </div>
          </div>

          <!-- 留言版 -->
          <div class="left-comment-box">
            <div class="comment-title">悠语</div>
            <div class="comment-total">
              共 <span style="color: #aabbd5">{{ total }}</span> 条
            </div>
            <div class="add-comment">
              <div class="comment-input">
                <div class="comment-el-avatar">
                  <el-image
                    :src="commentImageUrl"
                    :preview-src-list="[commentImageUrl]"
                    style="display: none"
                    id="commentImageUrlAvatar"
                  >
                  </el-image>
                  <div style="cursor: pointer" @click="OpenAvatarImage()">
                    <el-avatar :src="commentImageUrl"></el-avatar>
                  </div>
                </div>
                <div class="comment-el-input">
                  <el-input
                    type="textarea"
                    maxlength="500"
                    show-word-limit
                    :placeholder="commentPlaceholder"
                    v-model="comment"
                    rows="5"
                    @input="sensitiveWord()"
                  ></el-input>
                </div>
              </div>
              <div class="comment-buttons">
                <div class="comment-copy-button">
                  <el-button size="mini" @click="CopyUserInput()"
                    >粘贴</el-button
                  >
                </div>
                <div class="comment-send-button">
                  <el-button
                    size="mini"
                    type="primary"
                    :disabled="comment == ''"
                    @click="SayUserInputPublishComments()"
                    >发表</el-button
                  >
                </div>
              </div>
            </div>
            <div class="user-comments" v-if="leaveWords.length != 0">
              <div
                class="item"
                v-for="(item, index) in leaveWords"
                :key="index"
              >
                <div class="left-avatar">
                  <el-avatar
                    v-if="item.email != null && item.email.endsWith('@qq.com')"
                    :src="
                      'http://q2.qlogo.cn/headimg_dl?dst_uin=' +
                      item.email +
                      '&spec=100'
                    "
                  ></el-avatar>
                  <el-avatar
                    v-else
                    :src="require('@/assets/index/userComments.jpg')"
                  ></el-avatar>
                </div>
                <div class="right-box">
                  <div class="right-box-top">
                    <div class="right-box-top-left">
                      <span class="username">{{ item.username }}</span>
                      <span class="time">{{ item.time }}</span>
                    </div>
                    <div class="right-box-top-right">
                      <i
                        class="el-icon-magic-stick"
                        :class="{ showColor: loveChange(item.loveid) }"
                        @click="loveAddClick(item.uuid, item.loveid)"
                      >
                        {{ item.love }}</i
                      >
                      <i
                        v-if="item.id == id"
                        class="el-icon-delete"
                        @click="DeleteMyComment(item.uuid)"
                      ></i>
                    </div>
                  </div>
                  <div class="right-box-center">
                    {{ item.leaveword }}
                  </div>
                  <div class="right-box-footer">
                    <div class="right-box-footer-college">
                      {{ item.college }}
                    </div>
                    <div class="right-box-footer-major">
                      {{ item.major }}
                    </div>
                    <div class="right-box-footer-year">
                      {{ item.year }}
                    </div>
                  </div>
                </div>
              </div>
              <el-pagination
                style="margin-top: 2vh"
                :total="total"
                background=""
                @current-change="handleCurrentChange"
                size="small"
              ></el-pagination>
            </div>
            <div class="user-comments-else" v-else>暂无留言</div>
          </div>
        </el-card>
      </div>
      <div class="right">
        <div class="right-box">
          <el-card>
            <div class="word-title">
              <div>目录</div>
              <div>
                <el-progress :percentage="percentage"></el-progress>
              </div>
            </div>
            <div class="come-to-words" @click="ToLeaveWordsHello">
              欢迎来到悠悠小语👋
            </div>
            <div class="introduce-words" @click="ToLeaveWordsIntorduce">
              留言板是为了什么？🤔
            </div>
            <div class="why-words" @click="ToLeaveWordsWhy">如何留言？📝</div>
            <div class="sponsor-words" @click="ToLeaveWordsMoney">赞助🤗</div>
            <div class="relation-words" @click="ToLeaveWordsEmail">
              联系方式
            </div>
            <div class="thank-words" @click="ToLeaveWordsThank">
              感谢您的支持🙏
            </div>
          </el-card>
        </div>
        <div>
          <IndexAboutMeVue></IndexAboutMeVue>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import IndexAboutMeVue from "../index/IndexAboutMe.vue";
export default {
  components: { IndexAboutMeVue },
  data() {
    return {
      documentHeight: null, //文档高度
      percentage: 0, //滚动条高度
      commentImageUrl: require("@/assets/index/comment.jpg"), //头像地址
      commentPlaceholder: "say出你的留言", //发表评论提示
      comment: "", //用户输入
      leaveWords: [], //获取评论的数组
      skip: 1, //处理留言条数
      total: 0, //翻页的条数
      id: localStorage.getItem("id"), //用户的id
      oldTime: "", //判断是否快速点赞
    };
  },
  mounted() {
    //初始化页面参数内容
    this.BlogDetailStart();
    //获取文档高度
    this.documentHeight = Math.max(
      document.body.scrollHeight,
      document.body.offsetHeight,
      document.documentElement.clientHeight,
      document.documentElement.scrollHeight,
      document.documentElement.offsetHeight
    );
    //获取用户留言条数
    this.GetLeaveWords();
    //获取用户的个人头像信息
    this.GetUserAvatar();
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.documentHeightScroll);
  },
  methods: {
    //刷新页面
    Refresh() {
      location.reload();
    },
    //判断是否点赞高亮
    loveChange(e) {
      const reg = new RegExp(this.id, "g");
      if (reg.test(e)) {
        return true;
      } else {
        return false;
      }
    },
    //翻页调用
    handleCurrentChange(skip) {
      this.skip = skip;
      this.GetLeaveWords();
    },
    //删除用户
    DeleteMyComment(id) {
      this.$axios
        .get("/LeaveWords/clickDeleteLeaveword?id=" + id)
        .then((res) => {
          if (res.data.code == 200) {
            this.$notify.success({ message: "Successfully delete" });
            this.GetLeaveWords();
          } else {
            this.$notify.error({ message: "Fail to delete" });
          }
        });
    },
    //对用户点赞进行操作
    loveAddClick(a, b) {
      const time = Date.now();
      if (this.oldTime != "") {
        if (this.oldTime - time < 100) {
          this.$notify.warning({ message: "Do not operate frequently" });
          return;
        }
      }
      const ifAdd = this.loveChange(b);
      if (ifAdd) {
        this.$axios
          .get(`/LeaveWords/DeleteLeaveWordsLoveid?uuid=${a}&id=${this.id}`)
          .then((res) => {
            if (res.data.code == 200) {
              this.GetLeaveWords();
              this.oldTime = Date.now();
              this.$notify.error({ message: "Oh， my!" });
              setTimeout(() => {
                this.oldTime = "";
              }, 100);
            }
          });
      } else {
        this.$axios
          .get(`/LeaveWords/AddLeaveWordsLoveid?uuid=${a}&id=${this.id}`)
          .then((res) => {
            if (res.data.code == 200) {
              this.GetLeaveWords();
              this.oldTime = Date.now();
              this.$notify.success({ message: "Thanks for liking" });
              setTimeout(() => {
                this.oldTime = "";
              }, 100);
            }
          });
      }
    },
    //发表留言
    SayUserInputPublishComments() {
      this.$axios
        .post(`/LeaveWords/AddLeaveWords`, {
          id: this.id,
          leavewords: this.comment,
        })
        .then((res) => {
          if (res.data.code == 200) {
            this.$notify.success({ message: "Successfully added" });
            this.comment = "";
            this.GetLeaveWords();
          } else {
            this.$notify.error({ message: "Fail to add" });
          }
        });
    },
    //敏感词过滤
    sensitiveWord() {
      const reg =
        /他妈|你妈|操|傻逼|鸡巴|JB|脑残|恶心|废物|有病|无耻|废物|妈的|智障/gi;
      if (reg.test(this.comment)) {
        this.comment = this.comment.replace(reg, "*");
      }
    },
    //用户发表留言粘贴
    async CopyUserInput() {
      if (navigator) {
        this.comment = await navigator.clipboard.readText();
      } else {
        this.$notify.error({ message: "Browser not supported" });
      }
    },
    //获取用户的个人头像信息
    GetUserAvatar() {
      this.$axios.get(`/Blog/GetUserAvatar?id=${this.id}`).then((res) => {
        let email = res.data.data.email;
        if (email != null && email.endsWith("@qq.com")) {
          email = email.slice(0, email.length - 7);
          this.commentImageUrl = `http://q2.qlogo.cn/headimg_dl?dst_uin=${email}&spec=100`;
        }
      });
    },
    //用户点击头像放大
    OpenAvatarImage() {
      document.getElementById("commentImageUrlAvatar").click();
    },
    //获取全部用户条数
    GetLeaveWords() {
      this.$axios
        .get(`/LeaveWords/GetLeaveWords?skip=${this.skip}`)
        .then((res) => {
          if (res.data.code == 200) {
            this.leaveWords = res.data.data.records;
            this.total = res.data.data.total;
            //监听滚动事件
            window.addEventListener("scroll", this.documentHeightScroll);
          }
        });
    },
    //监听计算页面高度赋值给滚动条
    documentHeightScroll() {
      //当前滚动高度
      //计算高度公式=当前滚动距离/（文档高度-可视高度【window.innerHeight】）*100
      const scrollTop =
        window.pageYOffset || document.documentElement.scrollTop;
      const scrollPercentage =
        (scrollTop / (this.documentHeight - window.innerHeight)) * 100;
      //如果计算值大于100取100
      if (scrollPercentage >= 100) {
        this.percentage = 100;
      } else {
        this.percentage = Math.round(scrollPercentage);
      }
    },
    //跳转到各个标签
    ToLeaveWordsHello() {
      this.$refs.LeaveWordsHello.scrollIntoView({ behavior: "smooth" });
    },
    //跳转到各个标签
    ToLeaveWordsIntorduce() {
      this.$refs.LeaveWordsIntorduce.scrollIntoView({ behavior: "smooth" });
    },
    //跳转到各个标签
    ToLeaveWordsWhy() {
      this.$refs.LeaveWordsWhy.scrollIntoView({
        behavior: "smooth",
      });
    },
    //跳转到各个标签
    ToLeaveWordsMoney() {
      this.$refs.LeaveWordsMoney.scrollIntoView({ behavior: "smooth" });
    },
    //跳转到各个标签
    ToLeaveWordsEmail() {
      this.$refs.LeaveWordsEmail.scrollIntoView({ behavior: "smooth" });
    },
    //跳转到各个标签
    ToLeaveWordsThank() {
      this.$refs.LeaveWordsThank.scrollIntoView({ behavior: "smooth" });
    },
    //初始化页面参数内容
    BlogDetailStart() {
      //设置背景色为透明色
      this.$bus.$emit("SetIndexTopBackgroundColor", 0);
      //到这个页面到顶部状态
      window.scrollTo({ top: 0, behavior: "auto" });
    },
  },
};
</script>

<style lang="less" scoped>
#back {
  font-size: 1vw;
  color: white;
  height: auto;
}
.header {
  width: 100%;
  height: 55vh;
  position: relative;
  z-index: 0;
  overflow: hidden;
  img {
    width: 100vw;
    height: 55vh;
    transform: scale(2);
