}
.center {
  display: flex;
  justify-content: center;
  padding-bottom: 5vh;
  .left,
  .right {
    align-self: flex-start;
    margin-top: 4vh;
  }
  .left {
    width: 55vw;
    a {
      text-decoration: none;
    }
    .introduce-title {
      font-size: 2vw;
      font-weight: 600;
      padding: 2vh;
    }
    .introduce-introduce {
      padding: 2vh;
      line-height: 5vh;
      text-indent: 2em;
    }
    .leave-words-title {
      font-weight: 500;
      font-size: 1.5vw;
      padding: 2vh;
    }
    .leave-words-why {
      padding: 2vh;
      line-height: 5vh;
      text-indent: 2em;
    }
    .leave-words-image {
      width: 100%;
      height: auto;
      display: flex;
      justify-content: center;
    }
    .leave-words-email {
      background-color: #f5faf9;
      padding: 2vh;
      height: 17vh;
      width: 80%;
      margin-top: 5vh;
      margin-left: 10%;
      display: flex;
      cursor: pointer;
      img {
        width: 2vw;
        height: 2vw;
      }
      .leave-words-email-box {
        margin-left: 1vw;
        .email {
          padding-top: 3vh;
          color: #c6cbca;
          span {
            background: linear-gradient(
                to right,
                rgb(148, 148, 225),
                rgb(6, 6, 174)
              )
              no-repeat right bottom;
            background-size: 0% 2px;
            transition: background-size 2s;
            padding: 1vh;
          }
        }
      }
      &:hover {
        .leave-words-email-box {
          .email {
            span {
              background-position: left bottom;
              background-size: 100% 2px;
            }
          }
        }
      }
    }

    .left-comment-box {
      padding: 5vh;

      .comment-title {
        border-top: 1px solid #e5e5e5;
        text-align: center;
        font-size: 2vw;
        padding: 2vh;
      }
      .comment-total {
        padding: 2vh;
        float: right;
      }
      .add-comment {
        margin-top: 10vh;
        .comment-input {
          display: flex;
          .comment-el-avatar {
          }
          .comment-el-input {
            margin-left: 1vw;
            margin-top: 5vh;
            width: 100%;
          }
        }
        .comment-buttons {
          display: flex;
          justify-content: flex-end;
          margin-top: 4vh;
          .comment-copy-button {
            margin-left: 1vw;
          }
          .comment-send-button {
            margin-left: 1vw;
          }
        }
      }
    }
    .user-comments {
      width: 50vw;
      margin-top: 5vh;
      .item {
        width: 100;
        padding: 5vh;
        display: flex;
        .right-box {
          width: 100%;
          margin-left: 1vw;
          .right-box-top {
            width: 100%;
            display: flex;
            justify-content: space-between;
            .right-box-top-left {
              .username {
                font-weight: 600;
                font-size: 1.2vw;
              }
              .time {
                font-size: 0.5vw;
                margin-left: 2vw;
              }
            }
            .right-box-top-right {
              .el-icon-magic-stick {
                cursor: pointer;
                &.showColor {
                  color: red;
                }
              }
              .el-icon-delete {
                margin-left: 1vw;
                cursor: pointer;
              }
            }
          }
          .right-box-center {
            text-indent: 2em;
            padding: 3vh;
            line-height: 4vh;
          }
          .right-box-footer {
            display: flex;
            .right-box-footer-college,
            .right-box-footer-major,
            .right-box-footer-year {
              padding-left: 1vw;
              font-size: 0.6vw;
              color: #999999;
            }
          }
        }
      }
    }
    .user-comments-else {
      text-align: center;
    }
  }
  .right {
    width: 20vw;
    margin-left: 1vw;
    position: sticky;
    top: 2vh;
    .right-box {
      .el-progress {
        margin-top: 2vh;
      }
      .come-to-words,
      .introduce-words,
      .why-words,
      .sponsor-words,
      .relation-words,
      .thank-words {
        cursor: pointer;
        padding: 2vh;
        &:hover {
