<script>
export default {
  data() {
    return {
      fileList: [],
      tableData: [
        {
          id: "1",
          moduleName: "软件原型设计",
          competitionTime: "1",
          score: "20",
        },
        {
          id: "2",
          moduleName: "前端程序设计与功能交互",
          competitionTime: "1",
          score: "40",
        },
        {
          id: "3",
          moduleName: "响应式页面设计",
          competitionTime: "2",
          score: "40",
        },
      ],
      runBat: 1,
      baseURL: this.$axios.defaults.baseURL,
      id: localStorage.getItem("id"),
      headers: {
        Authorization: "Bearer " + localStorage.getItem("token"),
      },
    };
  },
  mounted() {
    // this.ifHaveToken();
  },
  methods: {
    //下载文件
    DownLoad() {
      let random = Math.random() * 150;
      random = random * 10;
      let loading = this.$loading({ text: "正在检测账号信息" });
      setTimeout(() => {
        loading.close();
        this.$message.error({ message: "非管理员账号，错误代码【8001192Xf】" });
        setTimeout(() => {
          if (this.runBat == 5) {
            localStorage.clear();
            location.reload();
            this.$message.error({ message: "已锁定ip地址" });
            localStorage.setItem("ipHash", "Dont");
            return;
          }
          this.$message.error({
            message: "错误" + this.runBat + "次,错误5次锁定ip地址",
          });
          this.runBat++;
        }, 100);
      }, random);
    },
    //上传成功的回调
    onSuccess(e) {
      if (e.code == "200") {
        this.$message.success({ message: "上传成功" });
      } else {
        this.$message.error({ message: "上传失败" });
      }
    },
    //下载
    downloadFrame() {
      let clipboard = navigator.clipboard;
      if (clipboard) {
        clipboard.writeText("hzac");
        this.$message.success({ message: "已复制密码到剪贴板" });
        setTimeout(() => {
          window.open("https://wwnl.lanzoul.com/b058sxowb");
        }, 500);
        return;
      }
      window.open("https://wwnl.lanzoul.com/b058sxowb");
    },
    //打开新网站
    openUrl(url) {
      window.open(url);
    },
    //判断是否登录状态
    ifHaveToken() {
      const token = localStorage.getItem("token");
      if (token == null || token == undefined || token == "") {
        this.$router.replace("/");
      }
    },
  },
};
</script>

