<template>
  <div>
    <transition name="indexTopMove">
      <div ref="indexTopBox" v-show="showIndexTop" id="box">
        <div class="left">软件协会</div>
        <div class="right">
          <div class="item" v-for="(item, index) in rightList" :key="index">
            <div
              v-if="item.text !== '守望' || showManage"
              @click="IndexTopClick(index)"
              class="index-box"
            >
              <transition name="input">
                <input
                  @keyup.enter="UserSearchFun()"
                  @click.stop
                  ref="indexTopSearch"
                  type="text"
                  :class="{ active: showSearch }"
                  placeholder="Serach you want"
                  v-if="item.text == '灯塔'"
                  @blur="searchBlurFun()"
                  v-show="showSearch"
                  id="indexTopSearch"
                  v-model="userSearch"
                />
              </transition>
              <i :class="item.icon"></i>
              <span style="margin-left: 1vw"></span>{{ item.text }}
            </div>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  data() {
    return {
      rightList: [
        {
          icon: "el-icon-s-home",
          text: "启元",
        },
        {
          icon: "el-icon-search",
          text: "灯塔",
        },
        {
          icon: "el-icon-s-flag",
          text: "竞锦",
        },
        {
          icon: "el-icon-edit-outline",
          text: "悠语",
        },
        {
          icon: "el-icon-s-platform",
          text: "链悦",
        },
        {
          icon: "el-icon-help",
          text: "智谈",
        },
        {
          icon: "el-icon-headset",
          text: "心声",
        },
        {
          icon: "el-icon-setting",
          text: "守望",
        },
        {
          icon: "el-icon-user",
          text: "云轩",
        },
      ], //顶部提示
      showIndexTop: true, //是否展示顶部
      showSearch: false, //是否展示搜索
      userSearch: "", //用户搜索内容
      ifBlur: false, //判断是否失去焦点不让二次执行操作
      showManage: false, //是否展示管理员入口
      id: localStorage.getItem("id"), //获取用户id
    };
  },
  mounted() {
    //设置用户调用触发器设置颜色
    this.SetIndexTopBackgroundColor();
    //是否展示管理员入口
    this.ToAdmin();
  },
  methods: {
    //是否展示管理员入口
    ToAdmin() {
      this.$axios.get("/User/IfAdmin?id=" + this.id).then((res) => {
        if (res.data.code == 200) {
          this.showManage = true;
        } else {
          this.showManage = false;
        }
      });
    },
    //设置用户调用触发器设置颜色
    SetIndexTopBackgroundColor() {
      //触发器结果
      this.$bus.$on("SetIndexTopBackgroundColor", (e) => {
        //e==0则背景透明
        if (e == 0) {
          this.showIndexTop = true;
          document.getElementById("box").style.background = "#ffffff00";
          //e==1为黑色
        } else if (e == 1) {
          this.showIndexTop = true;
          document.getElementById("box").style.background = "#000000";
          //e==2隐藏顶部
        } else {
          this.showIndexTop = false;
        }
      });
    },
    //用户搜索内容
    UserSearchFun() {
      //如果用户输入为空则直接return
      if (this.userSearch == "" || this.userSearch.startsWith("/")) {
        return;
      }
      //如果用户不在搜索页面跳转至搜索页面
      if (this.$route.path != "/index/home") {
        this.$router.replace("/index/home");
        setTimeout(() => {
          this.$bus.$emit("IndexHomeUserSearch", this.userSearch);
        }, 0);
      }
      //触发器搜索方法
      this.$bus.$emit("IndexHomeUserSearch", this.userSearch);
    },
    //失去焦点的时候方法
    searchBlurFun() {
      // this.showSearch = false;
      if (this.userSearch.trim() == "") {
        //隐藏搜索框
        this.showSearch = false;
        //通知serachFun不再执行
        this.ifBlur = true;
      }
    },
    //搜索方法
    SearchFun() {
      //判断是否继续往下执行
      if (this.ifBlur) {
        //改变让下一次可以执行
        this.ifBlur = false;
        //执行完成不可继续往下执行
        return;
      }
      //判断是否执行
      if (!this.showSearch) {
        this.showSearch = true;
        this.$nextTick(() => {
          document.getElementById("indexTopSearch").focus();
        });
      } else {
        this.showSearch = false;
      }
    },

    //用户点击事件
    IndexTopClick(index) {
      //用户点击事件
      switch (index) {
        //跳转主页页面
        case 0:
          if (this.$route.path != "/index/home") {
            this.$router.replace("/index/home");
          }
          break;
        //跳转搜索
        case 1:
          this.SearchFun();
          break;
        //跳转竞赛页面
        case 2:
          if (this.$route.path != "/index/competition") {
            this.$router.replace("/index/competition");
          }
          break;
        //跳转留言页面
        case 3:
          if (this.$route.path != "/index/leave-words") {
            this.$router.replace("/index/leave-words");
          }
          break;
        //跳转网址分享
        case 4:
          if (this.$route.path != "/index/url") {
            this.$router.replace("/index/url");
          }
          break;
        //跳转讨论室
        case 5:
          if (this.$route.path != "/index/chat") {
            this.$router.replace("/index/chat");
          }
          break;
        //跳转意见箱
        case 6:
          if (this.$route.path != "/index/option") {
            this.$router.replace("/index/option");
          }
          break;
        //跳转设置
        case 7:
          if (this.$route.path != "/manage/users") {
            localStorage.setItem("router", this.$route.path);
            this.$router.push("/manage/users");
          }
          break;
        //跳转设置
        case 8:
          if (this.$route.path != "/index/user/info") {
            this.$router.push("/index/user/info");
          }
          break;
      }
    },
  },
  watch: {},
};
</script>

<style scoped lang="less">
#box {
  width: 100vw;
  height: 8vh;
  line-height: 8vh;
  display: flex;
  justify-content: space-between;
  position: fixed;
  z-index: 1000;
  background-color: #ffffff00;
  color: white;
  font-size: 1vw;
}
// 开始进入
.indexTopMove-enter {
  margin-top: -8vh;
  opacity: 0;
}
// 进入中的过程
.indexTopMove-enter-active {
  transition: all 2s;
}
// 进入完毕
.indexTopMove-enter-to {
  opacity: 1;
}
// 离开的过程
.indexTopMove-leave-active {
  transition: all 1s;
}
// 离开完毕
.indexTopMove-leave-to {
  margin-top: -8vh;
  opacity: 0;
}

.left {
  margin-left: 2vw;
}

.right {
  display: flex;
  justify-content: space-around;
  margin-right: 2vw;
  .item {
    .index-box {
      overflow: hidden;
      margin-left: 1vw;
      margin-right: 1vw;
      cursor: pointer;
      position: relative;
      overflow: hidden;
    }
    .index-box::after {
      display: block;
      width: 0;
      height: 1px;
      background-color: white;
      content: "";
      position: absolute;
      bottom: 20%;
      left: 50%;
      transition: 1s;
    }

    .index-box:hover::after {
      width: 100%;
      left: 0;
    }
    input {
      outline: none;
      margin-right: 1vw;
      font-size: 1vw;
      padding-left: 1vh;
      transition: transform 1s ease;
      color: #555;
      border: none;
      background: linear-gradient(#dbdae1, #a3aaba);
      box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.1),
        15px 15px 15px rgba(0, 0, 0, 0.1), 20px 20px 20px rgba(0, 0, 0, 0.1),
        30px 30px 30px rgba(0, 0, 0, 0.1), inset 1px 1px 2px #fff;
    }
    .input-enter-active,
    .input-leave-active {
      transition: transform 1s;
    }

    .input-enter,
    .input-leave-to {
      transform: translateX(5%);
    }
  }
}
</style>
