import router from "vue-router";
import NProgress from "nprogress";
import "nprogress/nprogress.css";

import ComputionLogin from "@/pages/login/ComputionLogin.vue";
import ComputionSign from "@/pages/sign/ComputionSign.vue";
import ComputionIndex from "@/pages/index/ComputionIndex.vue";
import CompetitionTopic from "@/pages/competition/CompetitionTopic.vue";
import QuestionBank from "@/pages/competition/QuestionBank.vue";
import QuestionCompetition from "@/pages/competition/QuestionCompetition.vue";
import ManageIndex from "@/pages/manage/ManageIndex.vue";

import IndexHome from "@/components/index/IndexHome.vue";
import BlogDetail from "@/components/index/BlogDetail.vue";
import CompetitionHome from "@/components/competition/CompetitionHome.vue";
import CompetitionRight from "@/components/competition/CompetitionRight.vue";
import LeaveWords from "@/components/words/LeaveWords.vue";
import ShareUrl from "@/components/share/ShareUrl.vue";
import OptionBox from "@/components/options/OptionBox.vue";
import ChatBug from "@/components/chat/ChatBug.vue";
import ChatDetail from "@/components/chat/ChatDetail.vue";
import ManageUsers from "@/components/manage/ManageUsers.vue";
import ManageUserEdit from "@/components/manage/ManageUserEdit.vue";
import ManageAdmin from "@/components/manage/ManageAdmin.vue";
import ManageLeaveWords from "@/components/manage/ManageLeaveWords.vue";
import ManageBlog from "@/components/manage/ManageBlog.vue";
import ManageUrl from "@/components/manage/ManageUrl.vue";
import IndexUser from "@/components/user/IndexUser.vue";
import MyInfo from "@/components/user/MyInfo.vue";
import MyFavorite from "@/components/user/MyFavorite.vue";
import MyBlog from "@/components/user/MyBlog.vue";
import MyWords from "@/components/user/MyWords.vue";
import MyUrl from "@/components/user/MyUrl.vue";
import PublishBlog from "@/components/blogs/PublishBlog.vue";

let routerView = new router({
  // mode:"history",
  //页面跳转自动回到顶部
  // scrollBehavior() {
  //   return { x: 0, y: 0 };
  // },
  routes: [
    {
      path: "/",
      redirect: "/index/home",
    },
    {
      path: "/index",
      redirect: "/index/home",
    },
    {
      path: "/C-login",
      component: ComputionLogin,
    },
    {
      path: "/C-sign",
      component: ComputionSign,
    },
    {
      path: "/topic",
      component: CompetitionTopic,
    },
    {
      path: "/question",
      component: QuestionBank,
      children: [
        {
          path: ":subjectType",
          component: CompetitionRight,
        },
      ],
    },
    {
      path: "/point-race",
      component: QuestionCompetition,
    },
    {
      path: "/index",
      component: ComputionIndex,
      children: [
        {
          path: "home",
          component: IndexHome,
        },
        {
          path: "blog",
          component: BlogDetail,
        },
        {
          path: "competition",
          component: CompetitionHome,
        },
        {
          path: "leave-words",
          component: LeaveWords,
        },
        {
          path: "url",
          component: ShareUrl,
        },
        {
          path: "option",
          component: OptionBox,
        },
        {
          path: "chat",
          component: ChatBug,
        },
        {
          path: "chat-detail",
          component: ChatDetail,
        },
        {
          path: "publish",
          component: PublishBlog,
        },
        {
          path: "user",
          component: IndexUser,
          children: [
            {
              path: "info",
              component: MyInfo,
            },
            {
              path: "favorite",
              component: MyFavorite,
            },
            {
              path: "blog",
              component: MyBlog,
            },
            {
              path: "words",
              component: MyWords,
            },
            {
              path: "url",
              component: MyUrl,
            },
          ],
        },
      ],
    },
    {
      path: "/manage",
      component: ManageIndex,
      children: [
        {
          path: "users",
          component: ManageUsers,
          children: [
            {
              path: "edit",
              component: ManageUserEdit,
            },
          ],
        },
        {
          path: "admin",
          component: ManageAdmin,
        },
        {
          path: "words",
          component: ManageLeaveWords,
        },
        {
          path: "blog",
          component: ManageBlog,
        },
        {
          path: "url",
          component: ManageUrl,
        },
      ],
    },
  ],
});

//跳转前增加进度条
routerView.beforeEach(async (to, from, next) => {
  NProgress.start();
  next();
});
//跳转后取消进度条
routerView.afterEach(() => {
  NProgress.done();
});

export default routerView;
