<template>
  <div>
    <div class="all">
      <el-container>
        <el-aside>
          <el-menu
            :default-active="activeIndex"
            class="el-menu-demo"
            background-color="#273446"
            text-color="#fff"
            router
            @select="selectQuestionBank"
          >
            <el-menu-item index="/index/competition">
              <i class="el-icon-s-flag"></i>
              <span slot="title">主页</span>
            </el-menu-item>
            <div v-for="(item, index) in subjectType" :key="index">
              <el-menu-item
                :index="String('/question/' + item.questionSubject)"
              >
                <span slot="title">{{ item.questionSubject }}</span>
              </el-menu-item>
            </div>
          </el-menu>
        </el-aside>
        <el-main class="right">
          <router-view></router-view>
        </el-main>
      </el-container>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      id: "",
      subjectType: [],
    };
  },
  computed: {
    activeIndex() {
      // 从路由参数中获取值，进行拼接
      const dynamicPath = "/question/" + this.$route.params.subjectType;
      return dynamicPath;
    },
  },
  mounted() {
    this.$bus.$on("CompetitionAdmin", () => {
      this.GetSubjectType();
    });
    this.id = localStorage.getItem("id");
    this.GetSubjectType();
    this.CompetitionCooperation();
  },
  methods: {
    //选择menu触发方法
    selectQuestionBank() {
      this.$bus.$emit("CompetitionAdmin");
    },
    //判断是否有权限
    CompetitionCooperation() {
      let id = localStorage.getItem("id");
      this.$axios.get(`/question/IfQuestionAdmin?userId=${id}`).then((res) => {
        if (res.data.code == 200) {
          this.GetSubjectType();
        } else {
          this.$notify.warning({ message: "Authorization failed" });
          this.$router.replace("/index/competition");
        }
      });
    },
    //获取题库分类
    GetSubjectType() {
      this.$axios.get("/question/GetSubjectType").then((res) => {
        this.subjectType = res.data.data;
        this.subjectType.unshift({ questionSubject: "管理" });
      });
    },
  },
};
</script>

<style lang="less" scoped>
.el-menu {
  text-align: center;
  height: 100vh;
}
.right {
  height: 100vh;
  width: auto;
  overflow: auto;
}
</style>