<template>
  <div id="back">
    <div class="header" ref="indexHeader">
      <div>
        <img :src="randomImage" alt="" />
      </div>
      <div class="title">
        <span @click="Refresh()">{{ bugList.title }}</span>
      </div>
      <div class="header-bottom">
        <div class="look">
          <el-tag size="small" type="success">算法复杂度分析</el-tag>
          <el-tag size="small" type="primary">链表</el-tag>
          <el-tag size="small" type="info">动态规划解题</el-tag>
          <el-tag size="small" type="warning">最优子结构问题</el-tag>
          <el-tag size="small" type="danger">最小生成树</el-tag>
        </div>
      </div>
    </div>
    <div class="center">
      <div class="left">
        <el-card>
          <div class="top">
            <div class="top-box">
              <div class="top-avatar">
                <el-avatar
                  v-if="user.email != null && user.email.endsWith('@qq.com')"
                  :src="
                    'http://q2.qlogo.cn/headimg_dl?dst_uin=' +
                    user.email +
                    '&spec=100'
                  "
                ></el-avatar>
                <el-avatar
                  v-else
                  :src="require('@/assets/index/userComments.jpg')"
                ></el-avatar>
              </div>
              <div class="top-right">
                <div class="top-right-header">
                  {{ user.college }} · {{ user.major }} · {{ user.username }} ·
                  发起于 {{ bugList.bugHelpTime }}
                </div>
              </div>
            </div>
          </div>
          <div class="bottom">
            <pre>{{ bugList.content }}</pre>
          </div>
          <!-- 留言版 -->
          <div class="left-comment-box">
            <div class="comment-title">优雅永不过时的神评</div>
            <div class="comment-total">
              共 <span style="color: #aabbd5">{{ total }}</span> 条
            </div>
            <div class="add-comment">
              <div class="comment-input">
                <div class="comment-el-avatar">
                  <el-image
                    :src="commentImageUrl"
                    :preview-src-list="[commentImageUrl]"
                    style="display: none"
                    id="commentImageUrlAvatar"
                  >
                  </el-image>
                  <div style="cursor: pointer" @click="OpenAvatarImage()">
                    <el-avatar :src="commentImageUrl"></el-avatar>
                  </div>
                </div>
                <div class="comment-el-input">
                  <el-input
                    type="textarea"
                    maxlength="10000"
                    show-word-limit
                    placeholder="say出你的想法la"
                    v-model="comment"
                    rows="5"
                    @input="sensitiveWord()"
                  ></el-input>
                  <el-input
                    type="textarea"
                    maxlength="15000"
                    show-word-limit
                    placeholder="粘贴你的代码块la"
                    v-model="codeContent"
                    rows="5"
                    @input="sensitiveWordCode()"
                    style="margin-top: 3vh"
                  ></el-input>
                </div>
              </div>
              <div class="comment-buttons">
                <div class="comment-copy-button">
                  <el-button size="mini" @click="CopyUserInput()"
                    >粘贴</el-button
                  >
                </div>
                <div class="comment-send-button">
                  <el-button
                    size="mini"
                    type="primary"
                    :disabled="comment == ''"
                    @click="SayUserInputPublishComments()"
                    >发表</el-button
                  >
                </div>
              </div>
            </div>
          </div>
          <div class="left-comments-user" v-if="speakList.length != 0">
            <div class="item" v-for="(item, index) in speakList" :key="index">
              <div class="comments-box">
                <div class="top-avatar">
                  <el-avatar
                    v-if="
                      item.user.email != null &&
                      item.user.email.endsWith('@qq.com')
                    "
                    :src="
                      'http://q2.qlogo.cn/headimg_dl?dst_uin=' +
                      item.user.email +
                      '&spec=100'
                    "
                  ></el-avatar>
                  <el-avatar
                    v-else
                    :src="require('@/assets/index/userComments.jpg')"
                  ></el-avatar>
                </div>
                <div class="top-right">
                  <div class="top-right-header">
                    <div>
                      {{ item.user.college }} · {{ item.user.major }} ·
                      {{ item.user.username }} · 发起于
                      {{ item.bugSpeak.speakTime }}
                    </div>
                    <div>
                      <i
                        class="el-icon-delete"
                        @click="
                          DeleteBugSpeak(
                            item.bugSpeak.id,
                            item.bugSpeak.bugDetailId
                          )
                        "
                        v-if="item.bugSpeak.userId == id"
                      ></i>
                    </div>
                  </div>
                  <div class="top-right-footer">
                    <div style="font-size: 13px; margin: 4%">
                      <pre>{{ item.bugSpeak.content }}</pre>
                    </div>
                    <div
                      v-if="item.bugSpeak.codeContent.length != 0"
                      style="margin-top: 2%"
                    >
                      <pre ref="hljsCode">{{ item.bugSpeak.codeContent }}</pre>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div style="text-align: center" v-else>暂无神评</div>
          <el-pagination
            :total="total"
            background=""
            size="small"
            @current-change="handlerChange"
          ></el-pagination>
        </el-card>
      </div>
      <div class="right">
        <div class="top">
          <el-card>
            <div class="introduce">
              <div>浏览人数：</div>
              <div>{{ bugList.look }}</div>
            </div>
            <div class="introduce">
              <div>参与人数：</div>
              <div>{{ bugList.speakCount + 1 }}</div>
            </div>
          </el-card>
        </div>
        <div class="bottom">
          <IndexAboutMeVue></IndexAboutMeVue>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import hljs from "highlight.js";
// import "/node_modules/cli-highlight/node_modules/highlight.js/styles/atom-one-dark-reasonable.css";
import "highlight.js/styles/atom-one-dark-reasonable.css";
import IndexAboutMeVue from "../index/IndexAboutMe.vue";
export default {
  components: { IndexAboutMeVue },
  data() {
    return {
      chatId: this.$route.query.chatId, //获取页面参数数据
      bugList: {}, //问题数据
      user: {}, //用户数据
      speakList: [], //评论的数据
      randomImage: require("@/assets/chat/1.png"), //随机图片数据
      total: 0, //总条数用于分页数据
      commentImageUrl: "", //用户头像数据
      comment: "", //用户评论
      id: localStorage.getItem("id"), //获取用户的id
      codeContent: "", //用户粘贴代码块
      skip: 1, //用于翻页数据
    };
  },
  mounted() {
    //初始化页面参数内容
    this.BlogDetailStart();
    //随机拼接参数更换图片
    this.RandomChangeImage();
    //获取当前问题的用户信息和问题信息
    this.GetBugHelpDetail();
    //获取用户的个人头像信息
    this.GetUserAvatar();
    //获取当前评论
    this.GetBugSpeakAll();
  },
  methods: {
    //删除评论
    DeleteBugSpeak(e1, e2) {
      this.$axios
        .get(`/BugHelp/DeleteBugSpeak?id=${e1}&bugDetailId=${e2}`)
        .then((res) => {
          if (res.data.code == 200) {
            this.$notify.success({ message: "Successfully delete" });
            this.GetBugHelpDetail();
            this.GetBugSpeakAll();
          } else {
            this.$message.error({ message: "Fail to delete" });
          }
        });
    },
    //用于翻页数据
    handlerChange(skip) {
      this.skip = skip;
      this.GetBugSpeakAll();
    },
    //获取当前评论条数
    GetBugSpeakAll() {
      this.$axios
        .get(
          `/BugHelp/GetBugSpeakAll?skip=${this.skip}&bugDetailId=${this.chatId}`
        )
        .then((res) => {
          if (res.data.code == 200) {
            this.speakList = res.data.data;
            this.$nextTick(() => {
              let code = this.$refs.hljsCode;
              if (code) {
                code.forEach((item) => {
                  hljs.highlightElement(item);
                });
              }
            });
          }
        });
    },
    //发表评论
    SayUserInputPublishComments() {
      if (this.comment == "") {
        this.$notify.warning({ message: "Please enter a comment" });
        return;
      }
      this.$axios
        .post("/BugHelp/AddBugSpeak", {
          bugDetailId: this.chatId,
          userId: this.id,
          content: this.comment,
          codeContent: this.codeContent,
        })
        .then((res) => {
          if (res.data.code == 200) {
            console.log(res.data);
            this.$notify.success({ message: "Successful publication" });
            this.GetBugSpeakAll();
            this.GetBugHelpDetail();
            this.comment = "";
            this.codeContent = "";
          } else {
            this.$notify.error({ message: "Publication failure" });
          }
        });
    },
    //用户发表评论粘贴
    async CopyUserInput() {
      if (navigator) {
        this.codeContent = await navigator.clipboard.readText();
      } else {
        this.$notify.error({ message: "Browser not supported" });
      }
    },
    //敏感词过滤
    sensitiveWord() {
      const reg =
        /他妈|你妈|操|傻逼|鸡巴|JB|脑残|恶心|废物|有病|无耻|废物|妈的|智障/gi;
      if (reg.test(this.comment)) {
        this.comment = this.comment.replace(reg, "*");
      }
    },
    //敏感词过滤
    sensitiveWordCode() {
      const reg =
        /他妈|你妈|操|傻逼|鸡巴|JB|脑残|恶心|废物|有病|无耻|废物|妈的|智障/gi;
      if (reg.test(this.codeContent)) {
        this.codeContent = this.comment.replace(reg, "*");
      }
    },
    //打开个人头像
    OpenAvatarImage() {
      document.getElementById("commentImageUrlAvatar").click();
    },
    //获取用户的个人头像信息
    GetUserAvatar() {
      this.$axios.get(`/Blog/GetUserAvatar?id=${this.id}`).then((res) => {
        let email = res.data.data.email;
        if (email != null && email.endsWith("@qq.com")) {
          email = email.slice(0, email.length - 7);
          this.commentImageUrl = `http://q2.qlogo.cn/headimg_dl?dst_uin=${email}&spec=100`;
        }
      });
    },
    //随机拼接参数更换图片
    RandomChangeImage() {
      let random = Math.floor(Math.random() * 10 + 1);
      this.randomImage = require(`@/assets/chat/${random}.png`);
    },
    //获取当前问题的用户信息和问题信息
    GetBugHelpDetail() {
      this.$axios
        .get("/BugHelp/BugHelpDetail?id=" + this.chatId)
        .then((res) => {
          if (res.data.code == 200) {
            this.bugList = res.data.data.bugHelp;
            this.user = res.data.data.user;
          }
        });
    },
    //刷新页面
    Refresh() {
      location.reload();
    },
    //初始化页面参数内容
    BlogDetailStart() {
      //设置背景色为透明色
      this.$bus.$emit("SetIndexTopBackgroundColor", 0);
      //到这个页面到顶部状态
      window.scrollTo({ top: 0, behavior: "auto" });
    },
  },
};
</script>
<style lang="less" scoped>
#back {
  font-size: 1vw;
  color: white;
  height: auto;
}
.header {
  width: 100%;
  height: 55vh;
  position: relative;
  z-index: 0;
  overflow: hidden;
  img {
    width: 100vw;
    height: 55vh;
    transform: scale(2);
    filter: brightness(0.3);
    position: absolute;
    z-index: -1;
  }
  .title {
    text-align: center;
    font-size: 2vw;
    margin-top: 20vh;
    span {
      position: relative;
      cursor: pointer;
      padding: 0.1vw;
    }
  }
  .title span::after {
    display: block;
    width: 0;
    height: 1px;
    background-color: white;
    content: "";
    position: absolute;
    bottom: 0;
    right: 0%;
    transition: 1s;
  }
  .title span:hover::after {
    left: 0;
    width: 100%;
  }
  .header-bottom {
    display: flex;
    justify-content: center;
    margin-top: 10vh;
    .look {
      .el-tag {
        margin-left: 1vw;
        cursor: pointer;
      }
    }
  }
}
.center {
  display: flex;
  justify-content: center;
  padding: 3vh;
  .left,
  .right {
    align-self: flex-start;
  }
  .left {
    width: 55vw;
    .top {
      width: 55vw;
      .top-box {
        display: flex;
        .top-right {
          margin-top: 4vh;
          font-size: 0.8vw;
          color: #999999;
          margin-left: 1vw;
        }
      }
    }
    .bottom {
      padding: 3vh;
      pre {
        white-space: pre-wrap;
      }
    }
    .left-comment-box {
      padding: 5vh;

      .comment-title {
        border-top: 1px solid #e5e5e5;
        text-align: center;
        font-size: 2vw;
        padding: 2vh;
      }
      .comment-total {
        padding: 2vh;
        float: right;
      }
      .add-comment {
        margin-top: 10vh;
        .comment-input {
          display: flex;
          .comment-el-avatar {
          }
          .comment-el-input {
            margin-left: 1vw;
            margin-top: 5vh;
            width: 100%;
          }
        }
        .comment-buttons {
          display: flex;
          justify-content: flex-end;
          margin-top: 4vh;
          .comment-copy-button {
            margin-left: 1vw;
          }
          .comment-send-button {
            margin-left: 1vw;
          }
        }
      }
    }
    .left-comments-user {
      padding: 5vh;
      .item {
        &:not(:last-child) {
          border-bottom: 1px solid #999999;
        }
        padding: 3vh;
        .comments-box {
          display: flex;
          .top-right {
            margin-top: 4vh;
            font-size: 0.8vw;
            color: #999999;
            margin-left: 1vw;
            .top-right-header {
              width: 40vw;
              display: flex;
              justify-content: space-between;
              i {
                cursor: pointer;
              }
            }
          }
          .top-right-footer {
            pre {
              white-space: pre-wrap;
            }
          }
          .el-pagination {
            padding: 3vh;
            padding-bottom: 5vh;
          }
        }
      }
    }
  }
  .right {
    width: 20vw;
    margin-left: 1vw;
    position: sticky;
    top: 2vh;
    .top {
      .introduce {
        margin-top: 3vh;
        display: flex;
        justify-content: space-between;
        color: #999999;
      }
    }
  }
}
</style>