<template>
  <div id="back">
    <div class="header" ref="indexHeader">
      <div>
        <img src="@/assets/options/back.png" alt="" />
      </div>
      <div class="title">
        <span @click="Refresh()">意见宝盒</span>
      </div>
      <div class="header-bottom">
        <div class="time">灵感梦工场，每个建议都是一场梦的起点。</div>
      </div>
    </div>
    <div class="center">
      <el-card shadow>
        <div v-if="showResult">
          <div>
            <el-form
              :model="form"
              :rules="rules"
              ref="OpinionRef"
              label-width="100px"
            >
              <el-form-item label="主要内容：" prop="title">
                <div class="input">
                  <el-input clearable v-model="form.title"></el-input>
                </div>
              </el-form-item>
              <el-form-item label="描述：" prop="content">
                <el-input
                  type="textarea"
                  rows="10"
                  show-word-limit
                  maxlength="1000"
                  v-model="form.content"
                ></el-input>
              </el-form-item>
              <el-form-item label="匿名：">
                <el-switch v-model="anonymity"></el-switch>
              </el-form-item>
              <el-form-item label="附件：">
                <el-upload
                  action="http://localhost:12345/upload"
                  multiple=""
                  :auto-upload="false"
                  :file-list="fileList1"
                  :on-change="onChange1"
                >
                  <el-button size="mini">点击上传附件</el-button>
                </el-upload>
                <span style="color: red; font-size: 10px"
                  >支持50MB以内附件，最多上传三个，已启用覆盖上传</span
                >
              </el-form-item>
              <el-form-item label="图片：">
                <el-upload
                  action="http://localhost:12345/upload"
                  multiple=""
                  list-type="picture"
                  :auto-upload="false"
                  :file-list="fileList2"
                  drag
                  :on-change="onChange2"
                >
                </el-upload>
                <span style="color: red; font-size: 10px"
                  >支持10MB以内图片，最多上传三张，已启用覆盖上传</span
                >
              </el-form-item>
              <el-form-item
                style="margin-top: 2%"
                label="意见类别："
                prop="all"
              >
                <el-select name="" clearable id="" v-model="form.all">
                  <el-option value="功能请求" el-selected>功能请求</el-option>
                  <el-option value="网站体验">网站体验</el-option>
                  <el-option value="网站改进">网站改进</el-option>
                  <el-option value="技术问题">技术问题</el-option>
                  <el-option value="安全问题">安全问题</el-option>
                  <el-option value="内容建议">内容建议</el-option>
                  <el-option value="设计请求">设计请求</el-option>
                  <el-option value="性能问题">性能问题</el-option>
                  <el-option value="支持请求">支持请求</el-option>
                  <el-option value="其它">其它</el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="优先级：" prop="num">
                <el-select name="" clearable id="" v-model="form.num">
                  <el-option value="低">低</el-option>
                  <el-option value="中">中</el-option>
                  <el-option value="高">高</el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="联系方式：" prop="phone">
                <el-input
                  clearable
                  v-model="form.phone"
                  style="width: 50%"
                  type="number"
                >
                  <el-select slot="prepend" v-model="form.selectPhone">
                    <el-option value="电话">电话</el-option>
                    <el-option value="邮箱">邮箱</el-option>
                    <el-option value="微信">微信</el-option>
                    <el-option value="QQ">QQ</el-option>
                  </el-select>
                </el-input>
              </el-form-item>
            </el-form>
            <div style="display: flex; justify-content: flex-end">
              <el-button
                style="background-color: #1e1e1e; color: white"
                @click="submit('OpinionRef')"
                >提交</el-button
              >
            </div>
          </div>
        </div>
        <el-result icon="success" v-else title="提交成功" :subTitle="subTitle">
          <template slot="extra">
            <el-button @click="changeResult()" type="primary">返回</el-button>
          </template>
        </el-result>
      </el-card>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        phone: "",
        title: "",
        content: "",
        all: "",
        num: "",
        selectPhone: "电话",
      },
      rules: {
        phone: [
          { required: true, message: "必填项" },
          { min: 8, max: 11, message: "电话号码有误" },
        ],
        title: [{ required: true, message: "必填项" }],
        all: [{ required: true, message: "必填项" }],
        content: [{ required: true, message: "必填项" }],
        num: [{ required: true, message: "必填项" }],
        selectPhone: [{ required: true, message: "必填项" }],
      },
      showResult: true,
      anonymity: true,
      showImg: true,
      img: "",
      subTitle: "五秒后自动返回",
      fileList1: [],
      fileList2: [],
    };
  },
  mounted() {
    //初始化页面参数内容
    this.BlogDetailStart();
  },
  methods: {
    //初始化页面参数内容
    BlogDetailStart() {
      //设置背景色为透明色
      this.$bus.$emit("SetIndexTopBackgroundColor", 0);
      //到这个页面到顶部状态
      window.scrollTo({ top: 0, behavior: "auto" });
    },
    //改变
    onChange1(file, fileList) {
      this.fileList1 = fileList.slice(-3);
    },
    onChange2(file, fileList) {
      if (
        !file.name.endsWith(".png") &&
        !file.name.endsWith(".jpg") &&
        !file.name.endsWith(".jpeg")
      ) {
        fileList.pop();
        this.$notify.error({ message: "Please upload pictures" });
        return;
      }
      this.fileList2 = fileList.slice(-3);
    },
    //返回
    changeResult() {
      this.showResult = true;
    },
    //确定必填项
    submit(formdata) {
      this.$refs[formdata].validate((e) => {
        if (e) {
          const loading = this.$loading({ text: "正在提交" });
          setTimeout(() => {
            loading.close();
            this.ToOpioion(formdata);
          }, 2000);
        } else {
          this.$message.warning({ message: "请正确填写" });
        }
      });
    },
    //修改图片
    changeImg() {
      let file = document.getElementById("file").files[0];
      let reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = (e) => {
        this.img = e.target.result;
      };
      this.showImg = false;
    },
    //提交建议
    ToOpioion(formdata) {
      this.showResult = false;
      this.$refs[formdata].resetFields();
      setTimeout(() => {
        this.subTitle = "感谢您的反馈";
      }, 2000);
      setTimeout(() => {
        if (this.showResult == false) {
          this.showResult = true;
        }
      }, 5000);
    },

    //刷新页面
    Refresh() {
      location.reload();
    },
  },
};
</script>

<style lang="less" scoped>
#back {
  font-size: 1vw;
  color: white;
  height: auto;
}
.header {
  width: 100%;
  height: 55vh;
  position: relative;
  z-index: 0;
  overflow: hidden;
  img {
    width: 100vw;
    height: 55vh;
    transform: scale(2);
    filter: brightness(0.3);
    position: absolute;
    z-index: -1;
  }
  .title {
    text-align: center;
    font-size: 2vw;
    margin-top: 20vh;
    span {
      position: relative;
      cursor: pointer;
      padding: 0.1vw;
    }
  }
  .title span::after {
    display: block;
    width: 0;
    height: 1px;
    background-color: white;
    content: "";
    position: absolute;
    bottom: 0;
    right: 0%;
    transition: 1s;
  }
  .title span:hover::after {
    left: 0;
    width: 100%;
  }
  .header-bottom {
    display: flex;
    justify-content: center;
    margin-top: 10vh;
    .look {
      margin-left: 10vh;
    }
  }
}
.center {
  padding: 3vh;
  .el-card {
    width: 70%;
    height: auto;
    margin: auto;
    .input {
      :deep(.el-input__inner) {
        border: 0px;
        border-bottom: 1px solid #1e1e1e;
        border-radius: 0px;
      }
    }
    :deep(.el-textarea__inner) {
      border: 1px solid #1e1e1e;
    }
  }
}
</style>