<template>
  <div class="quiz-container">
    <div v-show="showCompetition">
      <!-- 显示题目 -->
      <div v-if="questionShow != null">
        <h2 class="question">
          {{ currentQuestionIndex + 1 }} .【题目】 {{ questionShow.topic }}
          <span style="opacity: 0.5; font-size: 1vw"
            >({{ questionShow.score }}分)</span
          >
        </h2>

        <!-- 显示选项 -->
        <el-radio-group
          @input="radioInput(questionShow.id)"
          v-model="selectedOption"
          class="options-container"
        >
          <div>
            <el-radio :label="questionShow.a">A.{{ questionShow.a }}</el-radio>
          </div>
          <div>
            <el-radio :label="questionShow.b">B.{{ questionShow.b }}</el-radio>
          </div>
          <div>
            <el-radio :label="questionShow.c">C.{{ questionShow.c }}</el-radio>
          </div>
          <div>
            <el-radio :label="questionShow.d">D.{{ questionShow.d }}</el-radio>
          </div>
        </el-radio-group>
      </div>

      <div style="display: flex; justify-content: space-around">
        <!-- 上一题按钮 -->
        <el-button
          @click="prevQuestion"
          :disabled="currentQuestionIndex === 0"
          type="primary"
          round
          size="mini"
          class="nav-button prev-button"
        >
          上一题
        </el-button>
        <!-- 上一题按钮 -->
        <el-button
          @click="submitData()"
          type="success"
          round
          size="mini"
          class="nav-button prev-button"
        >
          提交
        </el-button>

        <!-- 下一题按钮 -->
        <el-button
          @click="nextQuestion"
          :disabled="currentQuestionIndex === questions.length - 1"
          type="primary"
          round
          size="mini"
          class="nav-button next-button"
        >
          下一题
        </el-button>
      </div>
    </div>
    <div>
      <el-empty
        description="请重置页面宽高"
        v-show="!showCompetition"
      ></el-empty>
    </div>
    <div class="footer">版权信息 © 软件协会竞赛</div>
    <div>
      <div class="select-option">
        <el-button
          @click="showSelectOptionSubject"
          style="background-color: black; color: white"
          size="mini"
          >Select</el-button
        >
      </div>
    </div>

    <!-- 提交按钮提交数据 -->
    <div v-if="ScoreVisible" class="modal-background">
      <el-card class="score-card">
        <div slot="header" class="clearfix">
          <span>得分情况</span>
          <el-button
            style="float: right; padding: 3px 0"
            type="text"
            @click="ScoreBeforeClose"
            >确定</el-button
          >
        </div>
        <p>最终得分: {{ finalScore }}</p>
      </el-card>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      questions: [],
      currentQuestionIndex: 0,
      selectedOption: null,
      params: "",
      pageNum: 1,
      showCompetition: true,
      loading: null,
      n: 1,
      ScoreVisible: false,
      finalScore: 0,
      return: "no",
      id: "",
      map: new Map(),
    };
  },
  created() {},
  mounted() {
    this.id = localStorage.getItem("id");
    this.UserQuestionList();
    this.ListenUserNavigator();
    window.addEventListener("beforeunload", this.beforeUnload);
  },
  computed: {
    //计算展示数据属性
    questionShow() {
      return this.questions[this.currentQuestionIndex];
    },
  },
  //销毁监听事件
  beforeDestroy() {
    window.removeEventListener("beforeunload", this.beforeUnload);
    this.map.clear();
  },
  methods: {
    //关闭最后展示的成绩框框
    ScoreBeforeClose() {
      this.ScoreVisible = false;
      this.return = "ok";
      this.$router.replace("/index/competition");
      this.$notify.success({ message: "Save successfully" });
    },
    //提交所选择的数据
    submitData() {
      this.$confirm("提交后不可继续作答", "积分赛", {
        type: "warning",
      })
        .then(() => {
          let dataObjectMap = {};
          this.map.forEach((value, key) => {
            dataObjectMap[key] = value;
          });
          this.$axios
            .post(
              `/question/SubmitQuestionIntegral?userId=${this.id}`,
              dataObjectMap
            )
            .then((res) => {
              this.ScoreVisible = true;
              this.finalScore = res.data.data;
            });
        })
        .catch(() => {});
    },
    //监听离开此页面事件
    beforeUnload(e) {
      e.returnValue = "关闭提示";
    },
    //是否选择学科题目
    showSelectOptionSubject() {
      this.$prompt("请输入密钥", "Soft", {
        type: "warning",
      })
        .then(() => {
          this.$notify.error({ message: "Key error" });
        })
        .catch(() => {});
    },
    //radio按钮选择事件
    radioInput(id) {
      this.map.set(id, this.selectedOption);
    },
    //监听用户屏幕是否小于1280像素
    ListenUserNavigator() {
      window.addEventListener("resize", () => {
        let winWidth =
          window.innerWidth ||
          document.body.clientWidth ||
          document.documentElement.clientWidth;
        if (winWidth < 1280) {
          this.showCompetition = false;
        } else {
          this.showCompetition = true;
        }
      });
    },
    //请求题目
    UserQuestionList() {
      this.$axios
        .get(
          `/question/UserQuestionList?subjectType=${this.params}&pageNum=${this.pageNum}`
        )
        .then((res) => {
          if (this.questions.length == 0) {
            this.questions = res.data.data.records;
          } else if (this.questions.length != 0) {
            let tempArr = [];
            tempArr = res.data.data.records;
            tempArr.forEach((item) => {
              this.questions.push(item);
            });
          }
        });
    },
    //上一题
    prevQuestion() {
      if (this.currentQuestionIndex > 0) {
        this.currentQuestionIndex--;
        let prevId = this.questionShow.id;
        let selectedOption = this.map.get(prevId);
        this.selectedOption = selectedOption;
      }
    },
    //下一题
    nextQuestion() {
      //开启未完成答题
      let id = this.questionShow.id;
      let getId = this.map.get(id);
      if (getId == null || getId == undefined) {
        this.$notify.warning({ message: "Please answer this question first" });
        return;
      }
      if (this.currentQuestionIndex >= this.questions.length - 2) {
        this.n++;
        this.UserQuestionList();
      }
      if (this.currentQuestionIndex < this.questions.length - 1) {
        this.currentQuestionIndex++;
        let nextId = this.questionShow.id;
        let selectedOption = this.map.get(nextId);
        this.selectedOption = selectedOption;
      }
    },
  },
  beforeRouteLeave(from, to, next) {
    if (this.return == "ok") {
      next();
      return;
    }
    if (confirm("是否离开积分赛？")) {
      this.$notify.success({ message: "Leave success" });
      next();
      this.$router.replace("/index/competition");
    } else {
      next(false);
    }
    this.map.clear();
  },
};
</script>

<style scoped>
.quiz-container {
  position: relative;
  width: 80vw;
  margin: auto;
  height: 90vh;
  background-color: #f4f7f9;
  border-radius: 2vw;
  box-shadow: 0 0 2vw rgba(0, 0, 0, 0.1);
  padding: 3vh;
  margin-top: 2vh;
  font-family: "Arial", sans-serif;
  overflow: auto;
}

.question {
  color: #333;
  margin-bottom: 2vw;
  text-align: left;
}

.el-radio {
  white-space: break-spaces;
  padding: 3vh;
}

.nav-button {
  width: 5vw;
  margin: 2vw;
}
.footer {
  position: fixed;
  color: #bdc3c7;
  font-size: 1vh;
  right: 2vh;
  bottom: 2vh;
}
.select-option {
  position: fixed;
  font-size: 1vh;
  right: 2vh;
  top: 2vh;
}
.modal-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}
.score-card {
  width: 30vw;
}
</style>

