<template>
  <div>
    <div class="" v-if="favorites.length != 0">
      <div class="item" v-for="(item, index) in favorites" :key="index">
        <el-card>
          <div class="title" @click="OnopenNewHref(item.content)">
            网址：{{ item.content }}
          </div>
          <div class="content">详情：{{ item.title }}</div>
          <div class="love">
            <i class="el-icon-magic-stick"> {{ item.love }}</i>

            <el-popconfirm
              title="是否删除？"
              @confirm="DeleteShareUrlCollector(item.uuid)"
            >
              <i class="el-icon-delete" slot="reference"> </i>
            </el-popconfirm>
          </div>
        </el-card>
      </div>
      <el-pagination
        background=""
        size="small"
        :total="total"
        @current-change="handeChange"
      ></el-pagination>
    </div>
    <div class="item-else" v-else>
      <el-card> 没有收藏网址呢 </el-card>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      id: localStorage.getItem("id"), //获取用户的个人id
      favorites: [], //存放自己的数组数据
      skip: 1, //处理用户收藏翻页数据
      total: 0, //处理用户收藏总条数
    };
  },
  mounted() {
    //获取用户收藏的网址
    this.MyShareUrlCollector();
  },
  methods: {
    //处理用户翻页
    handeChange(skip) {
      this.skip = skip;
      this.MyShareUrlCollector();
    },
    //处理用户删除网址
    DeleteShareUrlCollector(e) {
      this.$axios
        .get(
          `/ShareUrl/DeleteShareUrlCollector?userId=${this.id}&shareUrlId=${e}`
        )
        .then((res) => {
          if (res.data.code == 200) {
            this.$notify.success({ message: "Successfully delete" });
            this.MyShareUrlCollector();
          }
        });
    },
    //打开新网站
    OnopenNewHref(e) {
      window.open(e);
    },
    //获取用户所收藏的网址
    MyShareUrlCollector() {
      this.$axios
        .get(
          `/ShareUrl/MyShareUrlCollector?userId=${this.id}&skip=${this.skip}`
        )
        .then((res) => {
          if (res.data.code == 200) {
            this.total = res.data.data.total;
            this.favorites = res.data.data.list;
          }
        });
    },
  },
};
</script>

<style lang="less" scoped>
.item {
  padding: 3vh;
  .el-card {
    .title {
      padding-top: 3vh;
      cursor: pointer;
    }
    .content {
      padding-top: 3vh;
    }
    .love {
      padding-top: 3vh;
      display: flex;
      justify-content: flex-end;
      .el-icon-magic-stick {
        color: red;
        cursor: pointer;
      }
      .el-icon-delete {
        cursor: pointer;
        margin-left: 1vw;
      }
    }
  }
}
.item-else {
  text-align: center;
  color: black;
}
</style>