<template>
  <div>
    <div v-show="params == '管理'">
      <div class="top" style="display: flex; justify-content: space-between">
        <div>
          <i class="el-icon-search"></i>
          <span> 管理列表</span>
        </div>

        <div>
          <el-button
            size="mini"
            type="danger"
            plain
            icon="el-icon-plus"
            @click="ShowAddAdminSubject()"
            >新增</el-button
          >
        </div>
      </div>
      <div class="center" style="margin-top: 1%">
        <el-table :data="dataAdmin">
          <el-table-column label="编号" prop="id"></el-table-column>
          <el-table-column
            label="协会"
            prop="questionSubject"
          ></el-table-column>
          <el-table-column label="操作" prop="">
            <template slot-scope="scope">
              <el-popconfirm
                title="是否删除？"
                @confirm="QuestionDeleteSubject(dataAdmin[scope.$index].id)"
              >
                <el-button size="mini" slot="reference">删除</el-button>
              </el-popconfirm>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <div v-show="params != '管理'">
      <div class="top" style="display: flex; justify-content: space-between">
        <div>
          <i class="el-icon-search"></i>
          <span> 题目列表</span>
        </div>
        <div style="display: flex">
          <div style="margin-left: 1vw">
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-download"
              @click="ExcelDown()"
              >下载模板</el-button
            >
          </div>
          <div style="margin-left: 1vw">
            <el-upload
              ref="uploadQuestion"
              :limit="1"
              :action="action + '/question/ExcelImport'"
              :on-change="onUploadChangeExcel"
              :auto-upload="false"
              :headers="headers"
              :file-list="fileList"
              :on-success="onUploadQuestionSuccess"
            >
              <el-button size="mini" type="" icon="el-icon-upload"
                >导入题目</el-button
              >
            </el-upload>
          </div>
          <div style="margin-left: 1vw">
            <el-button
              size="mini"
              type="danger"
              plain
              icon="el-icon-delete"
              @click="DeleteAllQuestionList"
              >批量删除</el-button
            >
          </div>
          <div style="margin-left: 1vw">
            <el-button
              size="mini"
              type="success"
              plain
              icon="el-icon-plus"
              @click="AddQuestionBank()"
              >新增</el-button
            >
          </div>
        </div>
      </div>
      <div class="center" style="margin-top: 2%">
        <el-table :data="dataSubject" @selection-change="onSelectionChange">
          <el-table-column type="selection"></el-table-column>
          <el-table-column label="编号" prop="id"></el-table-column>
          <el-table-column label="题目" prop="topic"></el-table-column>
          <el-table-column label="A项" prop="a"></el-table-column>
          <el-table-column label="B项" prop="b"></el-table-column>
          <el-table-column label="C项" prop="c"></el-table-column>
          <el-table-column label="D项" prop="d"></el-table-column>
          <el-table-column label="分数" prop="score"></el-table-column>
          <el-table-column width="200" label="操作" prop="">
            <template slot-scope="scope">
              <el-button
                size="mini"
                slot="reference"
                @click="GetQuestionOne(dataSubject[scope.$index].id)"
                >修改</el-button
              >
              <el-popconfirm
                title="是否删除？"
                @confirm="DeleteQuestion(dataSubject[scope.$index].id)"
              >
                <el-button size="mini" slot="reference">删除</el-button>
              </el-popconfirm>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <div style="margin-top: 2%">
      <el-pagination
        v-show="total > 0 && params != '管理'"
        @current-change="onCurrentChange"
        :total="total"
      ></el-pagination>
    </div>

    <el-dialog title="添加协会" :visible="visible" :before-close="beforeClose">
      <el-form label-width="100px">
        <el-form-item label="编号">
          <el-input
            size="small"
            disabled
            placeholder="该字段由系统生成"
          ></el-input>
        </el-form-item>
        <el-form-item label="协会">
          <el-input size="small" v-model="subjectType"></el-input>
        </el-form-item>
      </el-form>
      <div style="display: flex; justify-content: flex-end">
        <el-button size="mini" type="" @click="beforeClose">取消</el-button>
        <el-button size="mini" type="primary" @click="AddAdminSubject"
          >确定</el-button
        >
      </div>
    </el-dialog>
    <el-dialog
      title="添加题目"
      :visible="visibleTopic"
      :before-close="beforeClose"
    >
      <el-form
        ref="AddQuestionBankRef"
        :rules="questionBankRules"
        :model="questionBank"
        label-width="100px"
      >
        <el-form-item label="编号">
          <el-input
            size="small"
            disabled
            v-model="questionBank.id"
            placeholder="该字段由系统生成"
          ></el-input>
        </el-form-item>
        <el-form-item prop="topic" label="题目">
          <el-input
            type="textarea"
            size="small"
            v-model="questionBank.topic"
          ></el-input>
        </el-form-item>
        <el-form-item prop="a" label="A选项">
          <el-input
            type="textarea"
            size="small"
            v-model="questionBank.a"
          ></el-input>
        </el-form-item>
        <el-form-item prop="b" label="B选项">
          <el-input
            type="textarea"
            size="small"
            v-model="questionBank.b"
          ></el-input>
        </el-form-item>
        <el-form-item prop="c" label="C选项">
          <el-input
            type="textarea"
            size="small"
            v-model="questionBank.c"
          ></el-input>
        </el-form-item>
        <el-form-item prop="d" label="D选项">
          <el-input
            type="textarea"
            size="small"
            v-model="questionBank.d"
          ></el-input>
        </el-form-item>
        <el-form-item prop="rightAnswers" label="正确答案">
          <el-input
            type="textarea"
            size="small"
            v-model="questionBank.rightAnswers"
          ></el-input>
        </el-form-item>
        <el-form-item label="协会">
          <el-input size="small" v-model="params" disabled></el-input>
        </el-form-item>
        <el-form-item prop="score" label="分数">
          <el-select v-model="questionBank.score">
            <el-option value="1">1</el-option>
            <el-option value="1.5">1.5</el-option>
            <el-option value="2">2</el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div style="display: flex; justify-content: flex-end">
        <el-button size="mini" type="" @click="beforeClose">取消</el-button>
        <el-button size="mini" type="primary" @click="validateQuestionBank()"
          >确定</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      params: "",
      dataAdmin: [],
      dataSubject: [],
      total: 0,
      pageNum: 1,
      visible: false,
      subjectType: "",
      questionBank: {
        id: null,
        topic: null,
        a: null,
        b: null,
        c: null,
        d: null,
        rightAnswers: null,
        score: "1",
        subjectType: null,
      },
      questionBankRules: {
        topic: [{ required: true, message: "请输入必填项" }],
        a: [{ required: true, message: "请输入必填项" }],
        b: [{ required: true, message: "请输入必填项" }],
        c: [{ required: true, message: "请输入必填项" }],
        d: [{ required: true, message: "请输入必填项" }],
        rightAnswers: [{ required: true, message: "请输入必填项" }],
        score: [{ required: true, message: "请输入必填项" }],
        subjectType: [{ required: true, message: "请输入必填项" }],
      },
      visibleTopic: false,
      DeleteQuestionList: [],
      action: this.$axios.defaults.baseURL,
      headers: {
        Authorization: "Bearer " + localStorage.getItem("token"),
      },
      fileList: [],
      successQuestionReload: false,
    };
  },
  mounted() {
    this.$bus.$on("CompetitionAdmin", () => {
      //延迟以达到停顿获取效果
      setTimeout(() => {
        this.params = this.$route.params.subjectType;
        if (this.params == "管理") {
          this.GetSubjectType();
        } else {
          this.GetQuestionBank();
        }
      }, 0);
    });
    this.params = this.$route.params.subjectType;
    if (this.params == "管理") {
      this.GetSubjectType();
    } else {
      this.GetQuestionBank();
    }
  },
  methods: {
    //文件上传成功的回调
    onUploadQuestionSuccess(e) {
      this.successQuestionReload = true;
      if (e.data == "插入成功") {
        this.$message.success({ message: "插入数据成功" });
        this.$bus.$emit("CompetitionAdmin");
        this.fileList = [];
        return;
      }
      this.$message.error({ message: "插入数据异常" });
    },
    //定义文件上传
    onUploadChangeExcel(file, fileList) {
      if (this.successQuestionReload) {
        this.successQuestionReload = false;
        return;
      }
      let fileListTemp = fileList[0];
      if (
        fileListTemp.raw.name != "template.xlsx" &&
        fileListTemp.name != "template.xlsx"
      ) {
        this.$message.error({ message: "请上传【template.xlsx】模板" });
        this.fileList = [];
        return;
      }
      this.fileList.push(fileListTemp);
      this.ExcelImport();
    },
    //调用上传模板
    ExcelImport() {
      this.$confirm("是否上传", "Soft", { type: "warning" })
        .then(() => {
          this.$refs.uploadQuestion.submit();
        })
        .catch(() => {
          this.$message.info({ message: "取消上传" });
          this.fileList = [];
        });
    },

    //模板下载
    ExcelDown() {
      this.$axios
        .get("/question/ExcelDown", {
          responseType: "blob",
        })
        .then((res) => {
          let blob = new Blob([res.data], { type: res.data.type });
          let link = document.createElement("a");
          let url = window.URL.createObjectURL(blob);
          link.href = url;
          link.download = "template.xlsx";
          link.style.display = "none";
          document.body.appendChild(link);
          link.click();
          link.parentNode.removeChild(link);
          window.URL.revokeObjectURL(url);
        });
    },
    //新增题目
    AddQuestionBank() {
      let keys = Object.keys(this.questionBank);
      keys.forEach((item) => {
        this.questionBank[item] = null;
      });
      this.questionBank.score = "1";
      this.visibleTopic = true;
    },
    //修改题目
    GetQuestionOne(id) {
      this.$axios.get(`/question/GetQuestionOne?id=${id}`).then((res) => {
        if (res.data.code == 200) {
          this.questionBank = res.data.data;
          this.visibleTopic = true;
        } else {
          this.$notify.error({ message: "获取失败" });
        }
      });
    },
    //批量删除题目
    DeleteAllQuestionList() {
      if (this.DeleteQuestionList.length == 0) return;
      let arr = [];
      this.DeleteQuestionList.forEach((item) => {
        arr.push(item.id);
      });
      this.$axios.post(`/question/DeleteAllQuestionList`, arr).then((res) => {
        if (res.data.code == "1") {
          this.$message.success({ message: "删除成功" });
          location.reload();
        } else {
          this.$message.error({ message: "删除失败" });
        }
      });
    },
    //批量删除题目数组分类
    onSelectionChange(list) {
      this.DeleteQuestionList = list;
    },
    //处理翻页操作
    onCurrentChange(pageNum) {
      this.pageNum = pageNum;
      this.$bus.$emit("CompetitionAdmin");
    },
    //删除题目
    DeleteQuestion(id) {
      this.$axios.get(`/question/DeleteQuestion?id=${id}`).then((res) => {
        if (res.data.code == 200) {
          this.$notify.success({ message: "删除成功" });
          this.$bus.$emit("CompetitionAdmin");
        } else {
          this.$notify.error({ message: "删除失败" });
        }
      });
    },
    //验证规则
    validateQuestionBank() {
      this.$refs.AddQuestionBankRef.validate((e) => {
        if (e) {
          this.AddQuestion();
        }
      });
    },
    //修改题目
    UpdateQuestion() {
      this.$axios
        .post(`/question/UpdateQuestion`, this.questionBank)
        .then((res) => {
          if (res.data.code == 200) {
            this.$notify.success({ message: "修改成功" });
            this.$bus.$emit("CompetitionAdmin");
            this.beforeClose();
          } else {
            this.$notify.error({ message: "修改失败" });
          }
        });
    },
    //添加题目
    AddQuestion() {
      if (this.questionBank.id != null) {
        this.UpdateQuestion();
        return;
      }
      if (
        this.questionBank.a == this.questionBank.b ||
        this.questionBank.a == this.questionBank.c ||
        this.questionBank.a == this.questionBank.d ||
        this.questionBank.b == this.questionBank.c ||
        this.questionBank.b == this.questionBank.d ||
        this.questionBank.c == this.questionBank.d
      ) {
        this.$notify.warning({ message: "选项重复" });
        return;
      }
      this.questionBank.subjectType = this.params;
      this.$axios
        .post(`/question/AddQuestion`, this.questionBank)
        .then((res) => {
          if (res.data.code == 200) {
            this.$notify.success({ message: "添加成功" });
            this.$bus.$emit("CompetitionAdmin");
            this.beforeClose();
          } else {
            this.$notify.error({ message: "添加失败" });
          }
        });
    },
    //删除学科
    QuestionDeleteSubject(id) {
      this.$axios
        .get(`/question/QuestionDeleteSubject?id=${id}`)
        .then((res) => {
          if (res.data.code == 200) {
            this.$notify.success({ message: "删除成功" });
            setTimeout(() => {
              this.$notify.warning({ message: "题目已保留" });
            }, 500);
            this.$bus.$emit("CompetitionAdmin");
          } else {
            this.$notify.error({ message: "删除失败" });
          }
        });
    },
    //新增学科内容
    AddAdminSubject() {
      if (this.subjectType == "") {
        this.$message.warning({ message: "请输入内容" });
        return;
      }
      this.$axios
        .get(`/question/AddAdminSubject?subjectType=${this.subjectType}`)
        .then((res) => {
          if (res.data.code == 200) {
            this.visible = false;
            this.$notify.success({ message: res.data.data });
            this.$bus.$emit("CompetitionAdmin");
          } else {
            this.$notify.error({ message: res.data.msg });
          }
          this.subjectType = "";
        });
    },
    //设置不可见性
    beforeClose() {
      this.visible = false;
      this.visibleTopic = false;
    },
    //添加学科管理
    ShowAddAdminSubject() {
      this.visible = true;
    },
    //获取所添加学科的题库
    GetQuestionBank() {
      this.$axios
        .get(
          `/question/QuestionList?subjectType=${this.params}&pageNum=${this.pageNum}`
        )
        .then((res) => {
          this.dataSubject = res.data.data.records;
          this.total = res.data.data.total;
        });
    },
    //获取题库分类
    GetSubjectType() {
      this.$axios.get("/question/GetSubjectType").then((res) => {
        this.dataAdmin = res.data.data;
      });
    },
  },
};
</script>

