<template>
  <div>
    <transition name="indexHomeArrowUp">
      <div class="arrow-up" v-show="showIndexHomeArrowUp">
        <i class="el-icon-chat-dot-square"></i
        ><i class="el-icon-arrow-up" @click="ArrowUpTo()"></i>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  data() {
    return {
      showIndexHomeArrowUp: true, //是否显示右下角的返回顶部
    };
  },
  mounted() {
    //根据主页面传来的值进行显示底部的
    this.IndexArrowUpMessage();
  },
  methods: {
    //监听用户回到顶部
    ArrowUpTo() {
      window.scrollTo({ top: 0, behavior: "smooth" });
    },
    //根据主页面传来的滚动高度来显示底部的出现与消失
    IndexArrowUpMessage() {
      setTimeout(() => {
        this.showIndexHomeArrowUp = false;
      }, 2000);
      this.$bus.$on("IndexArrowUpMessage", (show) => {
        if (show) {
          this.showIndexHomeArrowUp = true;
        } else {
          this.showIndexHomeArrowUp = false;
        }
      });
    },
  },
};
</script>

<style scoped lang="less">
.arrow-up {
  display: grid;
  font-size: 1vw;
  justify-content: center; /* 水平居中 */
  align-items: center; /* 垂直居中 */
  position: fixed;
  right: 0.5vw;
  bottom: 5vh;
  width: 1.5vw;
  height: 10vh;
  border-radius: 0.2vw;
  background-color: #6366f1;
  z-index: 9999;
  color: white;
  .el-icon-arrow-up:hover {
    transform: scale(1.1);
    cursor: pointer;
  }
}
.indexHomeArrowUp-enter {
  margin-right: -0.5vw;
  opacity: 0;
}
.indexHomeArrowUp-enter-active {
  transition: all 2s;
}
.indexHomeArrowUp-enter-to {
  opacity: 1;
}
.indexHomeArrowUp-leave-active {
  transition: all 1s;
}
.indexHomeArrowUp-leave-to {
  margin-right: -0.5vw;
  opacity: 0;
}
</style>