<template>
  <div id="back">
    <div class="header" ref="indexHeader">
      <div>
        <img src="@/assets/blogs/back.png" alt="" />
      </div>
      <div class="title">
        <span @click="Refresh()">未来筆記</span>
      </div>
      <div class="header-bottom">
        <div class="time">未来的足迹：探索科技前沿，思考人类未来的方向</div>
        <div class="time">灵感之潮：思考、创作、分享，与灵感的潮流同行</div>
      </div>
    </div>
    <div class="center">
      <div class="left">
        <quill-editor class="editor" v-model="form.content"></quill-editor>
      </div>
      <div class="right">
        <el-card>
          <div class="box">
            <div class="right-title">标题：</div>
            <div class="right-content">
              <el-input
                show-word-limit
                maxlength="50"
                clearable
                v-model="form.title"
              ></el-input>
            </div>
          </div>
          <div class="box">
            <div class="right-title">分类：</div>
            <div class="right-content">
              <el-select v-model="form.classify">
                <el-option value="前端"></el-option>
                <el-option value="后端"></el-option>
                <el-option value="算法"></el-option>
                <el-option value="其它"></el-option>
              </el-select>
            </div>
          </div>
          <div class="box">
            <div class="right-title">介绍：</div>
            <div class="right-content">
              <el-input
                clearable=""
                show-word-limit
                maxlength="150"
                type="textarea"
                v-model="form.introduce"
              ></el-input>
            </div>
          </div>
          <div class="box">
            <div class="right-title">着色：</div>
            <div class="right-content">
              <el-button size="small" type="danger" @click="OpenUrlImageCode"
                >代码着色</el-button
              >
            </div>
          </div>
          <div class="box">
            <div class="right-title">图片：</div>
            <div class="right-content">
              <el-upload
                :action="action + '/Blog/UploadImage'"
                :headers="headers"
                :limit="1"
                :on-success="onSuccessImage"
                :auto-upload="false"
                :on-change="onChange"
                ref="BlogImage"
              >
                <el-button size="small" type="success">上传图片</el-button>
                <div class="tip">只能上传jpg/png文件，且不超过5MB</div>
              </el-upload>
            </div>
          </div>
          <div class="box">
            <div class="right-title">提交：</div>
            <div class="right-content">
              <el-button size="small" type="primary" @click="SubmitFordata()"
                >提交</el-button
              >
            </div>
          </div>
        </el-card>
      </div>
    </div>
  </div>
</template>

<script>
import { quillEditor } from "vue-quill-editor";
export default {
  components: {
    quillEditor,
  },
  data() {
    return {
      form: {
        content: "", //发表博客的内容
        title: "",
        classify: "",
        id: localStorage.getItem("id"),
        introduce: "",
        img: "",
      }, //表单数据
      action: this.$axios.defaults.baseURL,
      headers: {
        Authorization: "Bearer " + localStorage.getItem("token"),
      },
      fileList: [], //图片数据
    };
  },
  mounted() {
    //初始化页面参数内容
    this.BlogDetailStart();
  },
  methods: {
    //插入博客数据
    InsertBlog() {
      if (this.form.img == "") {
        this.$notify.error({ message: "Blog image not obtained" });
        return;
      }
      this.$axios.post("/Blog/InsertBlog", this.form).then((res) => {
        if (res.data.code == 200) {
          this.form = {
            content: "", //发表博客的内容
            title: "",
            classify: "",
            id: localStorage.getItem("id"),
            introduce: "",
            img: "",
          };
          this.fileList.length = 0;
          this.$notify.success({ message: "Successfully upload" });
        } else {
          this.$notify.error({ message: "Fail to upload" });
        }
      });
    },
    //提交图片上传验证
    SubmitFordata() {
      if (
        this.form.title == "" ||
        this.form.classify == "" ||
        this.form.content == "" ||
        this.form.introduce == "" ||
        this.form.id == ""
      ) {
        this.$notify.error({ message: "Required fields are not filled in" });
        return;
      }
      if (this.fileList.length == 0) {
        this.$notify.error({ message: "Blog image not obtained" });
        return;
      }
      this.$refs.BlogImage.submit();
    },
    //图片获取的回调
    onChange(file, fileList) {
      this.fileList = fileList.slice(-1);
    },
    //图片上传成功的回调
    onSuccessImage(e) {
      if (e.code == 200) {
        this.form.img = e.data;
        this.$notify.success({
          message: "Image uploaded successfully, uploading content",
        });
        this.InsertBlog();
      } else {
        this.$notify.error({
          message: "Image upload failed, please upload again",
        });
      }
    },
    //代码着色
    OpenUrlImageCode() {
      window.open("https://carbon.now.sh/");
    },
    //初始化页面参数内容
    BlogDetailStart() {
      //设置背景色为透明色
      this.$bus.$emit("SetIndexTopBackgroundColor", 0);
      //到这个页面到顶部状态
      window.scrollTo({ top: 0, behavior: "auto" });
    },
    //刷新页面
    Refresh() {
      location.reload();
    },
  },
};
</script>

<style lang="less" scoped>
#back {
  font-size: 1vw;
  color: white;
  height: auto;
}
.header {
  width: 100%;
  height: 55vh;
  position: relative;
  z-index: 0;
  overflow: hidden;
  img {
    width: 100vw;
    height: 55vh;
    transform: scale(2);
    filter: brightness(0.3);
    position: absolute;
    z-index: -1;
  }
  .title {
    text-align: center;
    font-size: 2vw;
    margin-top: 20vh;
    span {
      position: relative;
      cursor: pointer;
      padding: 0.1vw;
    }
  }
  .title span::after {
    display: block;
    width: 0;
    height: 1px;
    background-color: white;
    content: "";
    position: absolute;
    bottom: 0;
    right: 0%;
    transition: 1s;
  }
  .title span:hover::after {
    left: 0;
    width: 100%;
  }
  .header-bottom {
    display: flex;
    justify-content: space-around;
    margin-top: 10vh;
    .look {
      margin-left: 10vh;
    }
  }
}
.center {
  display: flex;
  justify-content: center;
  padding: 5vh;
  .left,
  .right {
    align-self: flex-start;
  }
  .left {
    width: 55vw;
    .editor {
      padding-bottom: 10vh;
      width: 55vw;
      height: 300vh;
      color: black;
    }
  }
  .right {
    width: 20vw;
    margin-left: 1vw;
    position: sticky;
    top: 2vh;
    .el-card {
      .box {
        padding-top: 3vh;
        .right-title {
          font-size: 1vw;
          font-weight: 600;
        }
        .right-content {
          padding: 2vh;
          display: flex;
          justify-content: center;
          .tip {
            font-size: 0.7vw;
            margin-top: 2vh;
            color: #999999;
          }
        }
      }
    }
  }
}
</style>