<template>
  <div id="back">
    <div class="header" ref="indexHeader">
      <div>
        <img src="@/assets/users/back.png" alt="" />
      </div>
      <div class="title">
        <span @click="Refresh()">个人画廊</span>
      </div>
      <div class="header-bottom">
        <div class="time">时光拾遗，个人心灵驿站</div>
      </div>
    </div>
    <div class="center">
      <div class="left">
        <router-view></router-view>
      </div>
      <div class="right">
        <el-card>
          <div class="title-my-catalogue"><span>目录</span></div>
          <div
            :class="{ showColor: $route.path == '/index/user/info' }"
            @click="ChangeRightView(1)"
            class="title-my-info"
          >
            <span>个人信息</span>
          </div>
          <div
            :class="{ showColor: $route.path == '/index/user/favorite' }"
            @click="ChangeRightView(2)"
            class="title-my-favorite"
          >
            <span>个人收藏</span>
          </div>
          <div
            :class="{ showColor: $route.path == '/index/user/blog' }"
            @click="ChangeRightView(3)"
            class="title-my-blog"
          >
            <span>个人博客</span>
          </div>
          <div
            :class="{ showColor: $route.path == '/index/user/words' }"
            @click="ChangeRightView(4)"
            class="title-my-words"
          >
            <span>个人留言</span>
          </div>
          <div
            :class="{ showColor: $route.path == '/index/user/url' }"
            @click="ChangeRightView(5)"
            class="title-my-url"
          >
            <span>个人网址</span>
          </div>
          <div
            :class="{ showColor: $route.path == '' }"
            class="title-my-message"
          >
            <span>个人消息</span>
          </div>
          <div
            :class="{ showColor: $route.path == '' }"
            class="title-my-project"
          >
            <span>个人项目</span>
          </div>
          <div
            :class="{ showColor: $route.path == '' }"
            class="title-my-option"
          >
            <span>个人意见</span>
          </div>
        </el-card>
        <el-card class="show-blog" v-if="showBlog"
          ><div @click="ToPublishBlog()">发表博客</div></el-card
        >
        <el-card class="show-blog" v-if="showUrl"
          ><div @click="PublishLeaveWords">发表网址</div></el-card
        >
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      showBlog: false, //是否展示发表博客
      showUrl: false, //是否展示发表网址
    };
  },
  mounted() {
    //初始化页面参数内容
    this.BlogDetailStart();
    //判断是否展示发表内容数据
    this.ShowSend();
  },
  methods: {
    //打开发表留言
    PublishLeaveWords() {
      this.$bus.$emit("ShowPublishLeaveWords");
    },
    //前往发表留言页面
    ToPublishBlog() {
      if (this.$route.path != "/index/publish") {
        this.$router.push("/index/publish");
      }
    },
    //判断是否展示发表内容
    ShowSend() {
      this.showBlog = false;
      this.showUrl = false;
      if (this.$route.path == "/index/user/blog") {
        this.showBlog = true;
      }
      if (this.$route.path == "/index/user/url") {
        this.showUrl = true;
      }
    },
    //初始化页面参数内容
    BlogDetailStart() {
      //设置背景色为透明色
      this.$bus.$emit("SetIndexTopBackgroundColor", 0);
      //到这个页面到顶部状态
      window.scrollTo({ top: 0, behavior: "auto" });
    },
    //展示不同的页面
    ChangeRightView(changeViewId) {
      this.showBlog = false;
      this.showUrl = false;
      switch (changeViewId) {
        case 1:
          if (this.$route.path != "/index/user/info") {
            this.$router.replace("/index/user/info");
          }
          break;
        case 2:
          if (this.$route.path != "/index/user/favorite") {
            this.$router.replace("/index/user/favorite");
          }
          break;
        case 3:
          if (this.$route.path != "/index/user/blog") {
            this.showBlog = true;
            this.$router.replace("/index/user/blog");
          }
          break;
        case 4:
          if (this.$route.path != "/index/user/words") {
            this.$router.replace("/index/user/words");
          }
          break;
        case 5:
          if (this.$route.path != "/index/user/url") {
            this.showUrl = true;
            this.$router.replace("/index/user/url");
          }
          break;
      }
    },
    //刷新页面
    Refresh() {
      location.reload();
    },
  },
};
</script>

<style lang="less" scoped>
#back {
  font-size: 1vw;
  color: white;
  height: auto;
}
.header {
  width: 100%;
  height: 55vh;
  position: relative;
  z-index: 0;
  overflow: hidden;
  img {
    width: 100vw;
    height: 55vh;
    transform: scale(2);
    filter: brightness(0.3);
    position: absolute;
    z-index: -1;
  }
  .title {
    text-align: center;
    font-size: 2vw;
    margin-top: 20vh;
    span {
      position: relative;
      cursor: pointer;
      padding: 0.1vw;
    }
  }
  .title span::after {
    display: block;
    width: 0;
    height: 1px;
    background-color: white;
    content: "";
    position: absolute;
    bottom: 0;
    right: 0%;
    transition: 1s;
  }
  .title span:hover::after {
    left: 0;
    width: 100%;
  }
  .header-bottom {
    display: flex;
    justify-content: center;
    margin-top: 10vh;
    .look {
      margin-left: 10vh;
    }
  }
}
.center {
  display: flex;
  justify-content: center;
  padding: 5vh;
  .left,
  .right {
    align-self: flex-start;
  }
  .left {
    width: 55vw;
    .el-card {
    }
  }
  .right {
    margin-left: 1vw;
    width: 20vw;
    position: sticky;
    top: 2vh;
    .el-card {
      .title-my-catalogue {
        padding: 3vh;
        font-size: 1.5vw;
      }
      .title-my-info,
      .title-my-favorite,
      .title-my-blog,
      .title-my-words,
      .title-my-url,
      .title-my-message,
      .title-my-project,
      .title-my-option {
        font-size: 1vw;
        padding-top: 4vh;
        text-align: center;
        cursor: pointer;
        span {
          background: linear-gradient(
              to right,
              rgb(222, 222, 231),
              rgb(63, 63, 175),
              rgb(222, 222, 231)
            )
            no-repeat right bottom;
          background-size: 0px 2px;
          transition: background-size 2s;
          padding: 1vh;
        }
        &:hover {
          span {
            background-position: left bottom;
            background-size: 100% 2px;
          }
          color: blue;
        }
        &.showColor {
          color: red;
          span {
            background: linear-gradient(
                to right,
                rgb(222, 222, 231),
                rgb(194, 65, 65),
                rgb(222, 222, 231)
              )
              no-repeat right bottom;
            background-size: 100% 2px;
            padding: 1vh;
          }
        }
      }
      .title-my-message,
      .title-my-project,
      .title-my-option {
        color: #999999;
      }
    }
    .show-blog {
      margin-top: 3vh;
      text-align: center;
      cursor: pointer;
      &:hover {
        color: red;
      }
    }
  }
}
</style>