<template>
  <div id="back">
    <div class="header" ref="indexHeader">
      <div>
        <img src="@/assets/share/back.png" alt="" />
      </div>
      <div class="title">
        <span @click="Refresh()">链接港</span>
      </div>
      <div class="header-bottom">
        <div class="time">链接分享，传递快乐；网址相连，温馨永存。</div>
      </div>
    </div>
    <div class="center" ref="shareUrlRef">
      <div class="left">
        <div class="show-box" v-if="shares.length != 0">
          <div
            class="item"
            v-animate
            v-for="(item, index) in shares"
            :key="index"
          >
            <el-card>
              <div class="share-box" @click="OpenUrl(item.shareUrl.content)">
                <div class="item-left">
                  <el-avatar
                    v-if="
                      item.user?.email != null &&
                      item.user.email.endsWith('@qq.com')
                    "
                    :src="
                      'http://q2.qlogo.cn/headimg_dl?dst_uin=' +
                      item.user.email +
                      '&spec=100'
                    "
                  ></el-avatar>
                  <el-avatar
                    v-else
                    :src="require('@/assets/index/userComments.jpg')"
                  ></el-avatar>
                </div>
                <div class="right-box">
                  <div class="right-box-top">
                    <div class="right-box-top-left">
                      <span class="username">{{ item.user.username }}</span>
                    </div>
                    <div class="right-box-top-right">
                      <i
                        class="el-icon-magic-stick"
                        :class="{ showColor: loveChange(item.shareUrl.loveid) }"
                        @click.stop="
                          loveAddClick(item.shareUrl.uuid, item.shareUrl.loveid)
                        "
                      >
                        {{ item.shareUrl.love }}</i
                      >
                      <i
                        v-if="item.user.id == id"
                        class="el-icon-delete"
                        @click.stop="DeleteShareUrl(item.shareUrl.uuid)"
                      ></i>
                    </div>
                  </div>
                  <div class="right-box-content">
                    <span>网站：{{ item.shareUrl.content }}</span>
                  </div>
                  <div class="right-box-title">
                    <span>详情：{{ item.shareUrl.title }}</span>
                  </div>
                  <div class="right-footer">
                    <div class="right-box-footer">
                      <div class="right-box-footer-college">
                        {{ item.user.college }}
                      </div>
                      <div class="right-box-footer-major">
                        {{ item.user.major }}
                      </div>
                      <div class="right-box-footer-year">
                        {{ item.user.year }}
                      </div>
                      <div class="right-box-footer-year">
                        {{ item.shareUrl.classify }}
                      </div>
                    </div>
                    <div class="right-box-footer-buttons">
                      <el-button @click.stop="ShareShare()" size="small"
                        >分享</el-button
                      >
                      <el-button
                        @click.stop="Enshrine(item.shareUrl.uuid)"
                        size="small"
                        >收藏</el-button
                      >
                      <el-button
                        @click.stop="CopyShareUrl(item.shareUrl.content)"
                        size="small"
                        >复制</el-button
                      >
                    </div>
                  </div>
                </div>
              </div>
            </el-card>
          </div>
        </div>
        <div class="pagination">
          <el-pagination
            :total="total"
            background=""
            @current-change="handleCurrentChange"
          ></el-pagination>
        </div>
      </div>
      <div class="right">
        <div class="top">
          <el-card shadow>
            <div class="title">创意</div>
            <div class="introduce">敢不敢打开这个神秘链接？</div>
            <div class="choose">
              <el-select
                size="small"
                clearable=""
                v-model="classify"
                placeholder="分类"
                @change="ShareChange()"
              >
                <el-option value="前端"></el-option>
                <el-option value="后端"></el-option>
                <el-option value="算法"></el-option>
                <el-option value="其它"></el-option>
                <el-option value="热门"></el-option>
              </el-select>
            </div>
            <div class="search" @keyup.enter="SearchUrl">
              <el-input
                size="small"
                clearable=""
                v-model="search"
                placeholder="搜索"
              >
                <el-button
                  slot="append"
                  icon="el-icon-search"
                  @click="SearchUrl()"
                ></el-button>
              </el-input>
            </div>
          </el-card>
        </div>
        <div class="bottom">
          <IndexAboutMe></IndexAboutMe>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import IndexAboutMe from "../index/IndexAboutMe.vue";
export default {
  components: { IndexAboutMe },
  data() {
    return {
      shares: [], //分享网址的数组
      skip: 1, //翻页数据第一页
      sort: "sort", //是否进行排序内容
      classify: "", //是否进行分类查找数据
      search: "", //用户搜索网址
      total: 0, //分页查询的total条数，网址总条数
      id: localStorage.getItem("id"), //用户id
      oldTime: "", //创建事件阻止频繁操作点击
    };
  },
  mounted() {
    //初始化页面参数内容
    this.BlogDetailStart();
    //获取网址请求
    this.GetShareUrl();
  },
  methods: {
    //搜搜网址请求
    SearchUrl() {
      if (this.search == "" || this.search.startsWith("/")) {
        return;
      }
      this.skip = 1;
      this.sort = "love";
      this.GetShareUrl();
    },
    //分类选择操作
    ShareChange() {
      this.skip = 1;
      if (this.classify == "热门") {
        this.classify = "";
        this.sort = "love";
      } else if (this.classify != "") {
        this.sort = "love";
      } else {
        this.sort = "sort";
      }
      this.GetShareUrl();
    },
    //翻页
    handleCurrentChange(skip) {
      this.skip = skip;
      this.GetShareUrl();
      this.$refs.shareUrlRef.scrollIntoView({ behavior: "smooth" });
    },
    //复制网址请求
    CopyShareUrl(e) {
      if (navigator.clipboard && navigator.clipboard.writeText) {
        navigator.clipboard.writeText(e);
        this.$notify.success({ message: "Successful replication" });
      } else {
        this.$notify.error({
          message: "Clipboard permission is not open, please paste manually",
        });
      }
    },
    //收藏网址请求
    Enshrine(e) {
      this.$axios
        .get(`/ShareUrl/AddUserShareUrl?userId=${this.id}&shareUrlId=${e}`)
        .then((res) => {
          if (res.data.code == 200) {
            this.$notify.success({ message: "Successful collection" });
          } else {
            this.$notify.error({ message: res.data.msg });
          }
        });
    },
    //分享网址请求
    ShareShare() {
      this.CopyShareUrl();
      setTimeout(() => {
        let qq = prompt("Please enter the share qq number");
        let reg = /\d+/gi;
        if (reg.test(qq)) {
          this.OpenWindow("https://wpa.qq.com/wpa_jump_page?v=3&uin=" + qq);
        } else {
          this.$notify.error({ message: "Please enter the correct qq number" });
        }
      }, 500);
    },
    //监听被拦截操作
    OpenWindow(url) {
      return new Promise((resolve, reject) => {
        const newWindow = window.open(url, "_blank");
        if (newWindow) {
          resolve(newWindow);
        } else {
          reject(new Error("Popup blocked"));
        }
      });
    },
    //删除网址请求
    DeleteShareUrl(uuid) {
      this.$axios.get("/ShareUrl/DeleteShareUrl?uuid=" + uuid).then((res) => {
        if (res.data.code == 200) {
          this.$notify.success({ message: "Successfully delete" });
          if (this.shares.length == 0) {
            this.sort = "";
            this.search = "";
            this.skip = 1;
            this.classify = "";
          }
          this.GetShareUrl();
        }
      });
    },
    //对用户点赞进行操作
    loveAddClick(a, b) {
      const time = Date.now();
      if (this.oldTime != "") {
        if (this.oldTime - time < 100) {
          this.$notify.warning({ message: "Do not operate frequently" });
          return;
        }
      }
      const ifAdd = this.loveChange(b);
      if (ifAdd) {
        this.$axios
          .get(`/ShareUrl/DeleteShareUrlLoveid?uuid=${a}&id=${this.id}`)
          .then((res) => {
            if (res.data.code == 200) {
              this.GetShareUrl();
              this.oldTime = Date.now();
              this.$notify.error({ message: "Oh， my!" });
              setTimeout(() => {
                this.oldTime = "";
              }, 100);
            }
          });
      } else {
        this.$axios
          .get(`/ShareUrl/AddShareUrlLoveid?uuid=${a}&id=${this.id}`)
          .then((res) => {
            if (res.data.code == 200) {
              this.GetShareUrl();
              this.oldTime = Date.now();
              this.$notify.success({ message: "Thanks for liking" });
              setTimeout(() => {
                this.oldTime = "";
              }, 100);
            }
          });
      }
    },
    //判断是否点赞高亮
    loveChange(e) {
      const reg = new RegExp(this.id, "g");
      if (reg.test(e)) {
        return true;
      } else {
        return false;
      }
    },
    //打开新网站请求
    OpenUrl(url) {
      window.open(url);
    },
    //获取网址请求
    GetShareUrl() {
      this.$axios
        .get(
          `/ShareUrl/GetShareUrl?skip=${this.skip}&sort=${this.sort}&search=${this.search}&classify=${this.classify}`
        )
        .then((res) => {
          if (res.data.code == 200) {
            this.shares = res.data.data.data;
            this.total = res.data.data.total;
          }
        });
    },
    //刷新页面
    Refresh() {
      location.reload();
    },
    //初始化页面参数内容
    BlogDetailStart() {
      //设置背景色为透明色
      this.$bus.$emit("SetIndexTopBackgroundColor", 0);
      //到这个页面到顶部状态
      window.scrollTo({ top: 0, behavior: "auto" });
    },
  },
};
</script>

<style lang="less" scoped>
#back {
  font-size: 1vw;
  color: white;
  height: auto;
}
.header {
  width: 100%;
  height: 55vh;
  position: relative;
  z-index: 0;
  overflow: hidden;
  img {
    width: 100vw;
    height: 55vh;
    transform: scale(2);
    filter: brightness(0.3);
    position: absolute;
    z-index: -1;
  }
  .title {
    text-align: center;
    font-size: 2vw;
    margin-top: 20vh;
    span {
      position: relative;
      cursor: pointer;
      padding: 0.1vw;
    }
  }
  .title span::after {
    display: block;
    width: 0;
    height: 1px;
    background-color: white;
    content: "";
    position: absolute;
    bottom: 0;
    right: 0%;
    transition: 1s;
  }
  .title span:hover::after {
    left: 0;
    width: 100%;
  }
  .header-bottom {
    display: flex;
    justify-content: center;
    margin-top: 10vh;
    .look {
      margin-left: 10vh;
    }
  }
}
.center {
  display: flex;
  justify-content: center;
  padding-bottom: 5vh;
  .left,
  .right {
    align-self: flex-start;
    margin-top: 4vh;
  }
  .left {
    width: 55vw;
    margin-top: 3vh;
    .item {
      padding-top: 3vh;
      width: 55vw;
      .el-card {
        border-radius: 5px;
        .share-box {
          display: flex;
          cursor: pointer;
          &:hover {
            .right-box {
              .right-box-title,
              .right-box-content {
                span {
                  background-size: 100% 2px;
                  background-position: left bottom;
                }
              }
            }
          }
        }
        .item-left {
        }

        .right-box {
          width: 100%;
          margin-left: 1vw;
          .right-box-top {
            width: 100%;
            display: flex;
            justify-content: space-between;
            .right-box-top-left {
              .username {
                font-weight: 600;
                font-size: 1.2vw;
              }
              .time {
                font-size: 0.5vw;
                margin-left: 2vw;
              }
            }
            .right-box-top-right {
              .el-icon-magic-stick {
                cursor: pointer;
                &.showColor {
                  color: red;
                }
              }
              .el-icon-delete {
                margin-left: 1vw;
                cursor: pointer;
              }
            }
          }
          .right-box-title,
          .right-box-content {
            span {
              background: linear-gradient(
                  to right,
                  rgb(240, 230, 230),
                  rgb(165, 5, 5),
                  rgb(240, 230, 230)
                )
                no-repeat right bottom;
              background-size: 0px 2px;
              padding: 1vh;
              transition: background-size 2s;
            }
          }
          .right-box-content {
            text-indent: 2em;
            padding-top: 3vh;
            line-height: 4vh;
          }
          .right-box-title {
            text-indent: 2em;
            padding-top: 1vh;
            line-height: 4vh;
            padding-bottom: 3vh;
          }
          .right-footer {
            display: flex;
            justify-content: space-between;
            .right-box-footer {
              display: flex;
              .right-box-footer-college,
              .right-box-footer-major,
              .right-box-footer-year {
                padding-left: 1vw;
                font-size: 0.6vw;
                color: #999999;
              }
            }
          }
        }
      }
    }
    .pagination {
      padding: 5vh;
    }
  }
  .right {
    position: sticky;
    top: 2vh;
    width: 20vw;
    margin-left: 1vw;
    .top {
      .title {
        font-weight: 500;
        font-size: 1.5vw;
      }
      .introduce {
        text-indent: 2em;
        padding: 3vh;
      }
      .choose {
        .el-select {
        }
      }
      .search {
        padding-top: 3vh;
      }
    }
  }
}
</style>