<template>
  <div id="back">
    <div class="header" ref="indexHeader">
      <div>
        <img
          v-if="blogUserList.img != ''"
          :src="'https://8.130.43.227/images/' + blogUserList.img"
          alt=""
        />
        <img v-else src="@/assets/image/backimg.jpg" alt="" />
      </div>
      <div class="classify">
        <span>{{ blogUserList.classify }}</span>
      </div>
      <div class="title">
        <span>{{ blogUserList.title }}</span>
      </div>
      <div class="header-bottom">
        <div class="time">发布于：{{ blogUserList.time | formdata }}</div>
        <div class="look">
          <i class="el-icon-view"></i> {{ blogUserList.look }}
        </div>
      </div>
    </div>
    <div class="center">
      <div class="left">
        <el-card>
          <div class="ql-container ql-snow" style="border: none">
            <div class="ql-editor">
              <div v-if="showBlog" id="detail"></div>
              <div v-else style="text-align: center">
                {{ showBlogLoadingText }}
              </div>
            </div>
          </div>
          <div class="left-card-message">
            <div class="title" ref="BlogDetailCardBottom">Soft</div>
            <div class="introduce">
              你用英语都写不出来的东西就别指望用代码写了。
            </div>
          </div>
          <div class="left-card-box">
            <div class="card-box-author">作者：{{ userInfo[0] }}</div>
            <div class="card-box-statement">
              声明：本文采用<span
                @click="
                  OpenUrl(
                    'https://creativecommons.org/licenses/by-nc-sa/4.0/deed.zh-hans'
                  )
                "
              >
                CC BY-NC-SA 4.0 许可协议 </span
              >，转载请注明出处。
            </div>
            <div class="card-box-tip">
              Tip：Please comply with the
              <span @click="OpenUrl('https://www.gnu.org/licenses/')">GPL</span
              >.
            </div>
          </div>
          <div class="left-comment-box">
            <div class="comment-title" ref="commentTitle">Comment</div>
            <div class="comment-total">
              共 <span style="color: #aabbd5">{{ blogDetail }}</span> 条
            </div>
            <div class="add-comment">
              <div class="comment-input">
                <div class="comment-el-avatar">
                  <el-image
                    :src="commentImageUrl"
                    :preview-src-list="[commentImageUrl]"
                    style="display: none"
                    id="commentImageUrlAvatar"
                  >
                  </el-image>
                  <div style="cursor: pointer" @click="OpenAvatarImage()">
                    <el-avatar :src="commentImageUrl"></el-avatar>
                  </div>
                </div>
                <div class="comment-el-input">
                  <el-input
                    type="textarea"
                    maxlength="500"
                    show-word-limit
                    :placeholder="commentPlaceholder"
                    v-model="comment"
                    rows="5"
                    @input="sensitiveWord()"
                  ></el-input>
                </div>
              </div>
              <div class="comment-buttons">
                <div class="comment-say-button">
                  <el-button
                    size="mini"
                    type="info"
                    v-if="commentPlaceholder.startsWith('回复')"
                    @click="SayComments()"
                    >取消回复</el-button
                  >
                </div>
                <div class="comment-copy-button">
                  <el-button size="mini" @click="CopyUserInput()"
                    >粘贴</el-button
                  >
                </div>
                <div class="comment-send-button">
                  <el-button
                    size="mini"
                    type="primary"
                    :disabled="comment == ''"
                    @click="SayUserInputPublishComments()"
                    v-if="commentPlaceholder.startsWith('回复')"
                    >回复</el-button
                  >
                  <el-button
                    v-else
                    size="mini"
                    type="primary"
                    :disabled="comment == ''"
                    @click="PublishComment()"
                    >评论</el-button
                  >
                </div>
              </div>
            </div>
          </div>
          <div class="user-comments" v-if="userComments.length != 0">
            <div
              class="item"
              v-for="(item, index) in userComments"
              :key="index"
            >
              <div class="left-avatar">
                <el-avatar
                  v-if="
                    item.user.email != null &&
                    item.user.email.endsWith('@qq.com')
                  "
                  :src="
                    'http://q2.qlogo.cn/headimg_dl?dst_uin=' +
                    item.user.email +
                    '&spec=100'
                  "
                ></el-avatar>
                <el-avatar
                  v-else
                  :src="require('@/assets/index/userComments.jpg')"
                ></el-avatar>
              </div>
              <div class="right-box">
                <div class="right-box-top">
                  <div class="right-box-top-left">
                    <span class="username">{{ item.user.username }}</span>
                    <span class="time">{{ item.blogSpeak.blogSpeakTime }}</span>
                  </div>
                  <div class="right-box-top-right">
                    <i
                      class="el-icon-chat-round"
                      @click="
                        PublishUserComment(
                          item.user.username,
                          item.blogSpeak.id
                        )
                      "
                    ></i>
                    <i
                      v-if="item.blogSpeak.blogUserId == id"
                      class="el-icon-delete"
                      @click="DeleteMyComment(item.blogSpeak.id)"
                    ></i>
                  </div>
                </div>
                <div class="right-box-center">
                  {{ item.blogSpeak.blogSpeak }}
                </div>
                <div class="right-box-footer">
                  <div class="right-box-footer-college">
                    {{ item.user.college }}
                  </div>
                  <div class="right-box-footer-major">
                    {{ item.user.major }}
                  </div>
                  <div class="right-box-footer-year">{{ item.user.year }}</div>
                </div>
                <div class="right-box-comments-box">
                  <div
                    class="user-comments-box"
                    v-for="(comment, commentIndex) in item.blogSpeakSpeakUser"
                    :key="commentIndex"
                  >
                    <div class="comment-left-avatar">
                      <el-avatar
                        v-if="
                          comment.user.email != null &&
                          comment.user.email.endsWith('@qq.com')
                        "
                        :src="
                          'http://q2.qlogo.cn/headimg_dl?dst_uin=' +
                          comment.user.email +
                          '&spec=100'
                        "
                      ></el-avatar>
                      <el-avatar
                        v-else
                        :src="require('@/assets/index/userComments2.jpg')"
                      ></el-avatar>
                    </div>
                    <div class="comments-right">
                      <div class="right-box-comments-top">
                        <div class="right-box-comments-top-left">
                          <span class="username">{{
                            comment.user.username
                          }}</span>
                          <span class="time">{{
                            comment.blogSpeakSpeak.blogTime
                          }}</span>
                        </div>
                      </div>
                      <div class="right-box-comments-center">
                        {{ comment.blogSpeakSpeak.blogSpeakSpeak }}
                      </div>
                      <div class="right-box-comments-footer">
                        <div class="right-box-comments-footer-college">
                          {{ comment.user.college }}
                        </div>
                        <div class="right-box-comments-footer-major">
                          {{ comment.user.major }}
                        </div>
                        <div class="right-box-comments-footer-year">
                          {{ comment.user.year }}
                        </div>
                        <div
                          v-if="comment.blogSpeakSpeak.blogSpeakUser == id"
                          class="right-box-comments-footer-year"
                        >
                          <div
                            @click="DeleteSpeakSpeak(comment.blogSpeakSpeak.id)"
                            style="cursor: pointer"
                            class="el-icon-delete"
                          ></div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <el-pagination
              style="margin-top: 2vh"
              :total="blogDetail"
              background=""
              @current-change="handleCurrentChange"
              v-show="blogDetail >= 10"
              size="small"
            ></el-pagination>
          </div>
          <div class="user-comments-else" v-else>暂无评论</div>
        </el-card>
      </div>
      <div class="right">
        <div>
          <el-card>
            <div class="author-info">代码之心</div>
            <div class="college">学院：{{ userInfo[3] }}</div>
            <div class="major">专业：{{ userInfo[1] }}</div>
            <div class="year">年级：{{ userInfo[2] }}</div>
            <div class="username">作者：{{ userInfo[0] }}</div>
            <div class="publish-blog">发表博客数量：{{ userInfo[4] }}</div>
            <div class="introduce">
              文章介绍：
              <div style="text-indent: 2em">{{ blogUserList.introduce }}</div>
            </div>
            <div>目录</div>
            <div class="introduce">
              <el-progress :percentage="percentage"></el-progress>
            </div>
          </el-card>
        </div>
        <div>
          <IndexAboutMeVue></IndexAboutMeVue>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import IndexAboutMeVue from "./IndexAboutMe.vue";
import axios from "axios";
export default {
  components: { IndexAboutMeVue },
  data() {
    return {
      blogUserList: this.$route.query, //获取页面参数
      userInfo: [], //发表博客的用户信息
      blogDetail: 0, //博客评论条数
      showBlog: false, //是否展示文章内容
      showBlogLoadingText: "文章内容正在加载", //展示文章内容的文字
      commentImageUrl: require("@/assets/index/comment.jpg"), //头像地址
      id: localStorage.getItem("id"), //用户id
      commentPlaceholder: "say出你的想法", //发表评论提示
      comment: "", //用户输入
      skip: 1, //评论翻页操作
      userComments: [], //博客所有评论信息
      sayUserCommentsId: "", //回复评论的评论的id
      percentage: 0, //进度条进度
      documentHeight: 0, //文档高度位置
    };
  },
  filters: {
    //日期格式化
    formdata(timetemp) {
      const now = new Date(+timetemp).toLocaleString();
      return now;
    },
  },
  mounted() {
    //打印文字
    this.priText();
    //初始化页面参数
    this.BlogDetailStart();
    //发起获取博客的请求
    this.GetBlogDetail();
    //获取发表文章的人的详情
    this.GetBlogUserInfo();
    //获取用户的个人头像信息
    this.GetUserAvatar();
    //获取文章评论内容
    this.GetBlogSpeak();
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.documentHeightScroll);
  },
  methods: {
    //敏感词过滤
    sensitiveWord() {
      const reg =
        /他妈|你妈|操|傻逼|鸡巴|JB|脑残|恶心|废物|有病|无耻|废物|妈的|智障/gi;
      if (reg.test(this.comment)) {
        this.comment = this.comment.replace(reg, "*");
      }
    },
    //计算进度条
    documentHeightScroll() {
      const scrollTop =
        window.pageYOffset || document.documentElement.scrollTop;
      const scrollPercentage =
        (scrollTop / (this.documentHeight - window.innerHeight)) * 100;
      if (scrollPercentage >= 100) {
        this.percentage = 100;
      } else {
        this.percentage = Math.round(scrollPercentage);
      }
    },
    //翻页方法
    handleCurrentChange(skip) {
      this.skip = skip;
      this.GetBlogSpeak();
    },
    //删除回复的评论-二级评论
    DeleteSpeakSpeak(id) {
      this.$axios.get("/Blog/DeleteSpeakSpeak?id=" + id).then((res) => {
        if (res.data.code == 200) {
          this.$notify.success({ message: "Successfully delete" });
          this.GetBlogSpeak();
        } else {
          this.$notify.error({ message: "Fail to delete" });
        }
      });
    },
    //回复的评论-二级评论
    SayUserInputPublishComments() {
      this.$axios
        .post("/Blog/AddBlogSpeakSpeak", {
          blogSpeakSpeak: this.comment,
          blogSpeakUser: this.id,
          blogSpeakId: this.sayUserCommentsId,
        })
        .then((res) => {
          if (res.data.code == 200) {
            this.$message.success({ message: "Reply successfully" });
            this.comment = "";
            this.commentPlaceholder = "say出你的想法";
            this.GetBlogSpeak();
          }
        });
    },
    //取消回复某人
    SayComments() {
      this.commentPlaceholder = "say出你的想法";
    },
    //回复评论的评论
    PublishUserComment(username, id) {
      let commentTitle = this.$refs.commentTitle;
      commentTitle.scrollIntoView({ behavior: "smooth" });
      this.commentPlaceholder = "回复" + username + "的评论";
      this.sayUserCommentsId = id;
    },
    //删除文章评论内容
    DeleteMyComment(DeleteBlogSpeak) {
      this.$axios
        .get("/Blog/DeleteBlogSpeak?id=" + DeleteBlogSpeak)
        .then((res) => {
          if (res.data.code == 200) {
            this.$notify.success({ message: "Successfully delete" });
            this.GetBlogSpeak();
            this.GetBlogUserInfo();
          } else {
            this.$notify.error({ message: "Fail to delete" });
          }
        });
    },
    //给文章发表评论
    PublishComment() {
      //判断用户是否输入，若为输入则不允许发表评论
      if (this.comment == "" || this.comment.startsWith("/")) {
        this.$notify.error({ message: "Please enter correctly" });
        return;
      }
      //发起请求添加数据
      this.$axios
        .post("/Blog/AddBlogSpeak", {
          blogId: this.blogUserList.detail,
          blogSpeak: this.comment,
          blogUserId: this.id,
        })
        .then((res) => {
          if (res.data.code == 200) {
            this.comment = "";
            this.$notify.success({ message: "Review success" });
            this.GetBlogSpeak();
            this.GetBlogUserInfo();
          } else {
            this.$notify.error({ message: "Comment failure" });
          }
        });
    },
    //获取文章评论内容
    GetBlogSpeak() {
      this.$axios
        .get(
          `/Blog/GetBlogSpeak?skip=${this.skip}&id=${this.blogUserList.detail}`
        )
        .then((res) => {
          this.userComments = res.data.data;
        });
    },
    //打开用户头像
    OpenAvatarImage() {
      document.getElementById("commentImageUrlAvatar").click();
    },
    //用户发表评论粘贴
    async CopyUserInput() {
      if (navigator) {
        this.comment = await navigator.clipboard.readText();
      } else {
        this.$notify.error({ message: "Browser not supported" });
      }
    },
    //获取用户的个人头像信息
    GetUserAvatar() {
      this.$axios.get(`/Blog/GetUserAvatar?id=${this.id}`).then((res) => {
        let email = res.data.data.email;
        if (email != null && email.endsWith("@qq.com")) {
          email = email.slice(0, email.length - 7);
          this.commentImageUrl = `http://q2.qlogo.cn/headimg_dl?dst_uin=${email}&spec=100`;
        }
      });
    },
    //打开新网站
    OpenUrl(url) {
      window.open(url);
    },
    //打印文字
    async priText() {
      let addText = "...";
      for (let i = 0; i < addText.length; i++) {
        await this.sleep(500);
        this.showBlogLoadingText += addText[i];
      }
      await this.sleep(500);
      this.showBlogLoadingText = "文章内容正在加载";
      this.priText();
    },
    //返回等待速度
    sleep(ms) {
      return new Promise((resolve) => setTimeout(resolve, ms));
    },
    //获取发表文章的人的详情
    GetBlogUserInfo() {
      this.$axios
        .get("/Blog/GetBlogUserWithBlog?uuid=" + this.blogUserList.detail)
        .then((res) => {
          this.userInfo = res.data.data;
          this.blogDetail = +this.userInfo[5];
        });
    },
    //初始化页面参数内容
    BlogDetailStart() {
      //设置背景色为透明色
      this.$bus.$emit("SetIndexTopBackgroundColor", 0);
      //到这个页面到顶部状态
      window.scrollTo({ top: 0, behavior: "auto" });
    },
    //获取文章内容
    GetBlogDetail() {
      this.$axios
        .get("/Blog/BlogDetail?uuid=" + this.blogUserList.detail)
        .then((res) => {
          if (res.data.code == 200) {
            axios
              .get("https://8.130.43.227/blog/" + res.data.data.content)
              .then((response) => {
                //更改显示
                this.showBlog = true;
                this.$nextTick(() => {
                  document.getElementById("detail").innerHTML = response.data;
                  //计算滚动条高度
                  this.documentHeight = Math.max(
                    document.body.scrollHeight,
                    document.body.offsetHeight,
                    document.documentElement.clientHeight,
                    document.documentElement.scrollHeight,
                    document.documentElement.offsetHeight
                  );
                  window.addEventListener("scroll", this.documentHeightScroll);
                });
              })
              .catch((error) => {
                console.error(error);
              });
          }
        });
    },
  },
};
</script>

<style lang="less" scoped>
#back {
  font-size: 1vw;
  color: white;
  background-color: #f5f5f5;
  height: auto;
}
.header {
  width: 100%;
  height: 55vh;
  position: relative;
  z-index: 0;
  overflow: hidden;
  img {
    width: 100vw;
    height: 55vh;
    filter: brightness(0.3);
    position: absolute;
    z-index: -1;
  }
  .classify {
    text-align: center;
    margin-top: 15vh;
    transition: 0.25s;
    span {
      border: 0.1vw solid white;
      padding: 0.2vw;
      cursor: pointer;
    }
    &:hover {
      transform: translateY(-0.25em);
    }
  }
  .title {
    text-align: center;
    font-size: 2vw;
    margin-top: 10vh;
    span {
      position: relative;
      cursor: pointer;
      padding: 0.1vw;
    }
  }
  .title span::after {
    display: block;
    width: 0;
    height: 1px;
    background-color: white;
    content: "";
    position: absolute;
    bottom: 0;
    right: 0%;
    transition: 1s;
  }
  .title span:hover::after {
    left: 0;
    width: 100%;
  }
  .header-bottom {
    display: flex;
    justify-content: center;
    margin-top: 10vh;
    .look {
      margin-left: 10vh;
    }
  }
}
.center {
  display: flex;
  justify-content: center;
  background-color: #f5f5f5;
  padding-bottom: 5vh;
  .left,
  .right {
    align-self: flex-start; /* 确保每个部分独立对齐 */
    margin-top: 4vh;
  }
  .left {
    width: 55vw;
    .el-card {
      border-radius: 10px;
      padding: 2vh;
      .left-card-message {
        padding: 2vh;

        .title {
          font-size: 2vw;
          font-weight: 600;
          border-bottom: 1px solid #e5e5e5;
        }
        .introduce {
          margin-top: 2vw;
          float: right;
        }
      }

      .left-card-box {
        background-color: #f3f4f6;
        margin-top: 15vh;
        padding: 3vh;
        position: relative;
        height: 20vh;
        span {
          color: #aabbd5;
          cursor: pointer;
        }
        .card-box-author,
        .card-box-statement,
        .card-box-tip {
          padding: 2vh;
        }
        &::after {
          content: "";
          width: 1vh;
          height: 26vh;
          background-color: #000000;
          position: absolute;
          border-radius: 5px 0px 0px 5px;
          left: 0px;
          top: 0;
        }
      }
      .left-comment-box {
        padding: 5vh;

        .comment-title {
          text-align: center;
          font-size: 2vw;
        }
        .comment-total {
          padding: 2vh;
          float: right;
        }
        .add-comment {
          margin-top: 10vh;
          .comment-input {
            display: flex;
            .comment-el-avatar {
            }
            .comment-el-input {
              margin-left: 1vw;
              margin-top: 5vh;
              width: 100%;
            }
          }
          .comment-buttons {
            display: flex;
            justify-content: flex-end;
            margin-top: 4vh;
            .comment-copy-button {
              margin-left: 1vw;
            }
            .comment-send-button {
              margin-left: 1vw;
            }
          }
        }
      }
      .user-comments {
        width: 50vw;
        margin-top: 5vh;
        .item {
          width: 100;
          padding: 5vh;
          display: flex;
          .right-box {
            width: 100%;
            margin-left: 1vw;
            .right-box-top {
              width: 100%;
              display: flex;
              justify-content: space-between;
              .right-box-top-left {
                .username {
                  font-weight: 600;
                  font-size: 1.2vw;
                }
                .time {
                  font-size: 0.5vw;
                  margin-left: 2vw;
                }
              }
              .right-box-top-right {
                .el-icon-chat-round {
                  cursor: pointer;
                }
                .el-icon-delete {
                  margin-left: 1vw;
                  cursor: pointer;
                }
              }
            }
            .right-box-center {
              text-indent: 2em;
              padding: 3vh;
              line-height: 4vh;
            }
            .right-box-footer {
              display: flex;
              .right-box-footer-college,
              .right-box-footer-major,
              .right-box-footer-year {
                padding-left: 1vw;
                font-size: 0.6vw;
                color: #999999;
              }
            }
            .right-box-comments-box {
              .user-comments-box {
                margin-left: 1vw;
                width: 35.5vw;
                margin-top: 4vh;
                display: flex;
                border-bottom: 1px solid #aabbd5;
                .right-box-comments-top {
                  display: flex;
                  padding: 2vh;
                  width: 35.5vw;

                  margin-left: 1vw;
                  .right-box-comments-top-left {
                    .username {
                      font-weight: 600;
                      font-size: 1.2vw;
                    }
                    .time {
                      font-size: 0.5vw;
                      margin-left: 2vw;
                    }
                  }
                  .right-box-comments-top-right {
                    cursor: pointer;
                  }
                }
                .right-box-comments-center {
                  text-indent: 4em;
                }
                .right-box-comments-footer {
                  display: flex;
                  padding: 2vh;
                  .right-box-comments-footer-college,
                  .right-box-comments-footer-major,
                  .right-box-comments-footer-year {
                    padding-left: 1vw;
                    font-size: 0.6vw;
                    color: #999999;
                  }
                }
              }
            }
          }
        }
      }
      .user-comments-else {
        text-align: center;
      }
    }
  }
  .right {
    margin-left: 1vw;
    width: 20vw;
    position: sticky;
    top: 2vh;
    .el-card {
      border-radius: 10px;
      padding: 1vh;
      .author-info {
        text-align: center;
        font-size: 1.5vw;
        padding: 2vh;
      }
      .college,
      .major,
      .year,
      .username,
      .publish-blog,
