<template>
  <div>
    <div class="all">
      <el-container>
        <el-aside>
          <el-menu
            :default-active="activeIndex"
            class="el-menu-demo"
            background-color="#273446"
            text-color="#fff"
            router
          >
            <el-menu-item :index="UserRouter">
              <i class="el-icon-s-home"></i>
              <span slot="title">返回</span>
            </el-menu-item>
            <el-menu-item index="/manage/users">
              <i class="el-icon-user"></i>
              <span slot="title">成员管理</span>
            </el-menu-item>
            <el-menu-item index="/manage/admin">
              <i class="el-icon-setting"></i>
              <span slot="title">权限管理</span></el-menu-item
            >
            <el-menu-item index="/manage/words">
              <i class="el-icon-message"></i>
              <span slot="title">历届留言</span></el-menu-item
            >
            <el-menu-item index="/manage/blog">
              <i class="el-icon-help"></i>
              <span slot="title">个人博客</span></el-menu-item
            >
            <el-menu-item index="/manage/url">
              <i class="el-icon-s-promotion"></i>
              <span slot="title">网址收录</span></el-menu-item
            >
            <el-menu-item disabled>
              <i class="el-icon-upload"></i>
              <span slot="title">源码下载</span></el-menu-item
            >
            <el-menu-item disabled>
              <i class="el-icon-key"></i>
              <span slot="title">超级管理员</span></el-menu-item
            >
          </el-menu>
        </el-aside>
        <el-main class="right">
          <router-view></router-view>
        </el-main>
      </el-container>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      id: localStorage.getItem("id"), //获取用户的id
      activeIndex: this.$route.path, //获取当前页面路由
      UserRouter: "/index/home", //主页跳转路径
    };
  },
  mounted() {
    //判断是否是管理员
    this.ToAdmin();
    //从本地缓存中获取主页跳转路径
    this.ChangeRouter();
    //监听用户刷新动作
    window.addEventListener("beforeunload", this.beforeUnload);
  },
  beforeDestroy() {
    window.removeEventListener("beforeunload", this.beforeUnload);
  },
  methods: {
    //用户刷新重新读取缓存
    beforeUnload() {
      localStorage.setItem("router", this.UserRouter);
    },
    //从本地缓存中获取主页跳转路径
    ChangeRouter() {
      let UserRouter = localStorage.getItem("router");
      if (UserRouter != null) {
        this.UserRouter = UserRouter;
        localStorage.removeItem("router");
      }
    },
    //判断用户是否是管理员，不是管理员顶出去
    ToAdmin() {
      this.$axios.get("/User/IfAdmin?id=" + this.id).then((res) => {
        if (res.data.code != 200) {
          this.$router.replace("/index/home");
        }
      });
    },
  },
};
</script>

<style lang="less" scoped>
.el-menu {
  text-align: center;
  height: 100vh;
}
.right {
  height: 100vh;
  width: auto;
  overflow: auto;
}
</style>