<template>
  <div id="back">
    <div class="header" ref="indexHeader">
      <div>
        <img src="@/assets/chat/back.png" alt="" />
      </div>
      <div class="title">
        <span @click="Refresh()">灵感悠游</span>
      </div>
      <div class="header-bottom">
        <div class="time">心手相连，疑难皆解。开源魔法，问题不过昙花一现。</div>
      </div>
    </div>
    <div class="center">
      <div class="left">
        <div class="show-box" v-if="chats.length != 0">
          <div
            class="item"
            v-animate
            v-for="(item, index) in chats"
            :key="index"
            @click="OpenChat(item.id)"
          >
            <div class="item-box">
              <el-card shadow>
                <div class="title">
                  {{ item.title }}
                </div>
                <div class="content">{{ item.content }}</div>
                <div class="footer">
                  <div class="look">
                    <i class="el-icon-view"> {{ item.look }}</i>
                  </div>
                  <div class="speak-count">
                    <i class="el-icon-chat-square"> {{ item.speakCount }}</i>
                  </div>
                  <div class="chat-time">{{ item.bugHelpTime }}</div>
                </div>
              </el-card>
            </div>
          </div>
          <el-pagination
            :total="total"
            background=""
            @current-change="handleCurrentChange"
          ></el-pagination>
        </div>
        <div v-else class="else">
          <el-card>{{ showChatText }}</el-card>
        </div>
      </div>
      <div class="right">
        <div class="right-top">
          <el-card>
            <div class="top">
              <div class="title">欢迎来到我们的讨论社区！</div>
              <div class="introduce">
                我们的目标是建立一个开放、友善的讨论空间，让大家能够分享见解、解决问题，并获得有价值的反馈。这里是你表达观点、学到新知识的地方。请遵循我们的社区规则，保持友善，愿您在这里度过愉快的时光。
              </div>
            </div>
            <div class="top">
              <div class="title">为什么加入我们？</div>
              <div class="introduce">
                <span>多元观点：</span> 与来自不同背景的人交流，获得全新的观点。
              </div>
              <div class="introduce">
                <span>问题解决：</span> 在社区中得到帮助，解决你遇到的问题。
              </div>
              <div class="introduce">
                <span>分享经验：</span>
                与他人分享你的知识和经验，为社区贡献价值。
              </div>
            </div>
            <div class="top">
              <div class="title">如何参与？</div>
              <div class="introduce">
                在感兴趣的话题下发表评论或发起新话题。
              </div>
              <div class="introduce">尊重他人，保持友善的讨论氛围。</div>
            </div>
          </el-card>
        </div>
        <div>
          <IndexAboutMeVue></IndexAboutMeVue>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import IndexAboutMeVue from "../index/IndexAboutMe.vue";
export default {
  components: { IndexAboutMeVue },
  data() {
    return {
      chats: [], //存放讨论的数组
      skip: 1, //进行分页操作
      total: 0, //翻页条数
      showChatText: "讨论内容正在加载", //给后缀增加...
    };
  },
  mounted() {
    //初始化页面参数内容
    this.BlogDetailStart();
    //打印文字
    this.priText();
    //获取讨论数据
    this.GetBugHelpAll();
  },
  methods: {
    //打开详情页
    OpenChat(chatId) {
        this.$axios.get("/BugHelp/AddBugHelpLook?id=" + chatId).then();
      this.$router.push({
        path: "/index/chat-detail",
        query: {
          chatId: chatId,
        },
      });
    },
    //打印文字
    async priText() {
      let addText = "...";
      for (let i = 0; i < addText.length; i++) {
        await this.sleep(500);
        this.showChatText += addText[i];
      }
      await this.sleep(500);
      this.showChatText = "讨论内容正在加载";
      this.priText();
    },
    //返回等待速度
    sleep(ms) {
      return new Promise((resolve) => setTimeout(resolve, ms));
    },
    //翻页操作
    handleCurrentChange(skip) {
      this.skip = skip;
      this.GetBugHelpAll();
    },
    //获取所有的问题
    GetBugHelpAll() {
      this.$axios
        .get("/BugHelp/GetBugHelpAll?skip=" + this.skip)
        .then((res) => {
          if (res.data.code == 200) {
            this.chats = res.data.data.records;
            this.total = res.data.data.total;
          }
        });
    },
    //刷新页面
    Refresh() {
      location.reload();
    },
    //初始化页面参数内容
    BlogDetailStart() {
      //设置背景色为透明色
      this.$bus.$emit("SetIndexTopBackgroundColor", 0);
      //到这个页面到顶部状态
      window.scrollTo({ top: 0, behavior: "auto" });
    },
  },
};
</script>

<style lang="less" scoped>
#back {
  font-size: 1vw;
  color: white;
  height: auto;
}
.header {
  width: 100%;
  height: 55vh;
  position: relative;
  z-index: 0;
  overflow: hidden;
  img {
    width: 100vw;
    height: 55vh;
    transform: scale(2);
    filter: brightness(0.3);
    position: absolute;
    z-index: -1;
  }
  .title {
    text-align: center;
    font-size: 2vw;
    margin-top: 20vh;
    span {
      position: relative;
      cursor: pointer;
      padding: 0.1vw;
    }
  }
  .title span::after {
    display: block;
    width: 0;
    height: 1px;
    background-color: white;
    content: "";
    position: absolute;
    bottom: 0;
    right: 0%;
    transition: 1s;
  }
  .title span:hover::after {
    left: 0;
    width: 100%;
  }
  .header-bottom {
    display: flex;
    justify-content: center;
    margin-top: 10vh;
    .look {
      margin-left: 10vh;
    }
  }
}
.center {
  display: flex;
  padding: 3vh;
  justify-content: center;
  .left,
  .right {
    align-self: flex-start;
  }
  .left {
    width: 50vw;
    .show-box {
      .item {
        cursor: pointer;
        padding: 3vh;
        .item-box {
          .el-card {
            border-radius: 10px;
            .title {
              font-weight: 600;
              font-size: 1.5vw;
            }
            .content {
              padding: 3vh;
              text-indent: 2em;
              line-height: 8vh;
              overflow: hidden;
              display: -webkit-box;
              text-overflow: ellipsis;
              -webkit-line-clamp: 2;
              -webkit-box-orient: vertical;
            }
            .footer {
              color: #999999;
              display: flex;
              font-size: 0.7vw;
              .look {
                margin-left: 2vw;
              }
              .speak-count {
                margin-left: 2vw;
              }
              .chat-time {
                margin-left: 2vw;
              }
            }
          }
        }
      }
      .el-pagination {
        display: flex;
        justify-content: center;
        padding: 3vh;
      }
    }
    .else {
      text-align: center;
    }
  }
  .right {
    position: sticky;
    top: 2vh;
    margin-left: 1vw;
    width: 20vw;
    .right-top {
      .top {
        padding: 2vh;
        .title {
          font-weight: 600;
          font-size: 1.2vw;
        }
        .introduce {
          padding-top: 3vh;
          text-indent: 2em;
          line-height: 5vh;
        }
        span {
          font-weight: 600;
        }
      }
    }
  }
}
</style>