<template>
  <div>
    <div class="user-comments" v-if="leaveWords.length != 0">
      <div class="item" v-for="(item, index) in leaveWords" :key="index">
        <div class="left-avatar">
          <el-avatar
            v-if="item.email != null && item.email.endsWith('@qq.com')"
            :src="
              'http://q2.qlogo.cn/headimg_dl?dst_uin=' +
              item.email +
              '&spec=100'
            "
          ></el-avatar>
          <el-avatar
            v-else
            :src="require('@/assets/index/userComments.jpg')"
          ></el-avatar>
        </div>
        <div class="right-box">
          <div class="right-box-top">
            <div class="right-box-top-left">
              <span class="username">{{ item.username }}</span>
              <span class="time">{{ item.time }}</span>
            </div>
            <div class="right-box-top-right">
              <i class="el-icon-magic-stick"> {{ item.love }}</i>
              <i class="el-icon-delete" @click="DeleteMyComment(item.uuid)"></i>
            </div>
          </div>
          <div class="right-box-center">
            {{ item.leaveword }}
          </div>
          <div class="right-box-footer">
            <div class="right-box-footer-college">
              {{ item.college }}
            </div>
            <div class="right-box-footer-major">
              {{ item.major }}
            </div>
            <div class="right-box-footer-year">
              {{ item.year }}
            </div>
          </div>
        </div>
      </div>
      <el-pagination
        style="margin-top: 2vh"
        :total="total"
        background=""
        @current-change="handleCurrentChange"
        size="small"
      ></el-pagination>
    </div>
    <div class="user-comments-else" v-else>暂无留言</div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      total: 0, //评论条数
      leaveWords: [], //存放评论的数据
      skip: 1, //翻页数据
    };
  },
  mounted() {
    //获取全部用户条数
    this.GetLeaveWords();
  },
  methods: {
    //删除用户
    DeleteMyComment(id) {
      this.$axios
        .get("/LeaveWords/clickDeleteLeaveword?id=" + id)
        .then((res) => {
          if (res.data.code == 200) {
            this.$notify.success({ message: "Successfully delete" });
            this.GetLeaveWords();
          } else {
            this.$notify.error({ message: "Fail to delete" });
          }
        });
    },
    //翻页操作
    handleCurrentChange(skip) {
      this.skip = skip;
      this.GetLeaveWords();
    },
    //获取全部用户条数
    GetLeaveWords() {
      this.$axios
        .get(`/LeaveWords/GetLeaveWords?skip=${this.skip}`)
        .then((res) => {
          if (res.data.code == 200) {
            this.leaveWords = res.data.data.records;
            this.total = res.data.data.total;
            //监听滚动事件
            window.addEventListener("scroll", this.documentHeightScroll);
          }
        });
    },
  },
};
</script>

<style lang="less" scoped>
.user-comments {
  .item {
    width: 100%;
    padding: 5vh;
    display: flex;
    &:not(:last-child) {
      border-bottom: 1px solid #999999;
    }
    .right-box {
      width: 100%;
      margin-left: 1vw;
      .right-box-top {
        width: 100%;
        display: flex;
        justify-content: space-between;
        .right-box-top-left {
          .username {
            font-weight: 600;
            font-size: 1.2vw;
          }
          .time {
            font-size: 0.5vw;
            margin-left: 2vw;
          }
        }
        .right-box-top-right {
          .el-icon-magic-stick {
            cursor: pointer;
            &.showColor {
              color: red;
            }
          }
          .el-icon-delete {
            margin-left: 1vw;
            cursor: pointer;
          }
        }
      }
      .right-box-center {
        text-indent: 2em;
        padding: 3vh;
        line-height: 4vh;
      }
      .right-box-footer {
        display: flex;
        .right-box-footer-college,
        .right-box-footer-major,
        .right-box-footer-year {
          padding-left: 1vw;
          font-size: 0.6vw;
          color: #999999;
        }
      }
    }
  }
}
.user-comments-else {
  text-align: center;
}
</style>