<template>
  <div>
    <div class="left">
      <div v-if="articles.length != 0">
        <div
          class="item"
          @click="ToBlogDetail(item)"
          v-animate
          v-for="item in articles"
          :key="item.uuid"
        >
          <el-card>
            <div style="display: flex">
              <div class="left-left">
                <div class="left-bigTitle">
                  {{ item.title }}
                </div>
                <div class="left-classify">
                  <i class="el-icon-folder-opened"></i> {{ item.classify }}
                </div>
                <div class="left-subTitle">
                  {{ item.introduce }}
                </div>
                <div class="left-bottom">
                  <div class="left-bottom-time">
                    <i class="el-icon-date"></i> {{ item.time | formdata }}
                  </div>
                  <div class="left-bottom-look">
                    <i class="el-icon-view"></i> {{ item.love }}
                    <i
                      class="el-icon-delete"
                      @click.stop="DeleteMyBlog(item.uuid)"
                    ></i>
                  </div>
                </div>
              </div>
              <div class="left-right">
                <el-image
                  :src="'https://8.130.43.227/images/' + item.img"
                  :preview-src-list="[
                    'https://8.130.43.227/images/' + item.img,
                  ]"
                  v-if="item.img != ''"
                  @click.stop
                >
                  <div
                    slot="error"
                    style="
                      display: flex;
                      justify-content: center;
                      align-items: center;
                      height: 100%;
                    "
                    class="image-slot"
                  >
                    <i class="el-icon-picture-outline"></i>
                  </div>
                </el-image>
                <el-image
                  @click.stop
                  v-else
                  :src="blogImage"
                  :preview-src-list="[blogImage]"
                >
                  <div
                    slot="error"
                    style="
                      display: flex;
                      justify-content: center;
                      align-items: center;
                      height: 100%;
                    "
                    class="image-slot"
                  >
                    <i class="el-icon-picture-outline"></i>
                  </div>
                </el-image>
              </div>
            </div>
          </el-card>
        </div>
        <div class="indexCenter-left-bottom">
          <el-pagination
            background=""
            :total="total"
            size="small"
            @current-change="handleCurrentChange"
            v-show="total >= 5"
          ></el-pagination>
        </div>
      </div>
      <div style="text-align: center" v-else>
        <el-card>{{ showBlogLoadingText }}</el-card>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      articles: [], //博客数据
      total: 0, //博客条数
      showBlogLoadingText: "博客内容正在加载", //未显示的时候博客数据
      skip: 1, //翻页内容
      id: localStorage.getItem("id"), //获取用户id
      blogImage: require("@/assets/image/backimg.jpg"), //博客数据
    };
  },
  filters: {
    formdata(timetemp) {
      const now = new Date(+timetemp).toLocaleString();
      return now;
    },
  },
  mounted() {
    //打印文字
    this.priText();
    //获取用户博客内容
    this.GetMyblogInfo();
  },
  methods: {
    //处理用户删除自己的博客
    DeleteMyBlog(e) {
      if (confirm("确定删除吗？")) {
        this.$axios.get("/Blog/DeleteMyBlog?uuid=" + e).then((res) => {
          if (res.data.code == 200) {
            this.GetMyblogInfo();
          }
        });
      }
    },
    //处理用户获取自己的博客
    GetMyblogInfo() {
      this.$axios
        .get(`/Blog/GetMyblogInfo?id=${this.id}&skip=${this.skip}`)
        .then((res) => {
          if (res.data.code == 200) {
            if (res.data.data.length != 0) {
              this.articles = res.data.data.records;
            } else {
              this.showBlogLoadingText = "未发表博客文章";
            }
          }
        });
    },
    //翻页数据
    handleCurrentChange(skip) {
      this.skip = skip;
      this.GetMyblogInfo();
    },
    //等待描述
    sleep(ms) {
      return new Promise((resolve) => setTimeout(resolve, ms));
    },
    //打印文字
    async priText() {
      let addText = "...";
      for (let i = 0; i < addText.length; i++) {
        await this.sleep(500);
        this.showBlogLoadingText += addText[i];
      }
      await this.sleep(500);
      this.showBlogLoadingText = "博客内容正在加载";
      this.priText();
    },
    //跳转博客详情，并带上内容
    ToBlogDetail(e) {
      //设置颜色为透明色
      this.$bus.$emit("SetIndexTopBackgroundColor", 0);
      //更换核心内容数据
      this.$router.push({
        path: "/index/blog",
        query: {
          detail: e.uuid,
          title: e.title,
          classify: e.classify,
          introduce: e.introduce,
          time: e.time,
          look: e.love,
          img: e.img,
        },
      });
    },
  },
};
</script>

<style lang="less" scoped>
.left {
  width: 55vw;
  .item {
    display: flex;
    margin-top: 3vh;
    width: 55vw;
    border-radius: 10px;
    background-color: #ffffff;
    line-height: 6vh;
    height: 33vh;
    overflow: auto;
    position: relative;
    &:hover {
      .el-image {
        transform: scale(1.1);
      }
      .left-bigTitle::after {
        width: 100%;
        left: 0;
      }
    }
  }
  .indexCenter-left-bottom {
    display: flex;
    justify-content: center;
    padding: 10vh;
  }

  .left-left {
    cursor: pointer;
    width: 35vw;
  }
  .left-bigTitle {
    padding-top: 1vh;
    padding-left: 2vw;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    font-size: 1.5vw;
    color: #4b41cc;
    position: relative;
  }
  .left-bigTitle::after {
    display: block;
    width: 0;
    height: 1px;
    background-color: #928cee;
    content: "";
    position: absolute;
    bottom: 0;
    right: 0%;
    transition: 1s;
  }

  .left-classify {
    margin-left: 2vw;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    opacity: 0.5;
    font-size: 1vw;
    position: relative;
  }
  .left-subTitle {
    padding-left: 2vw;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    opacity: 0.8;
    font-size: 1vw;
  }
  .left-bottom {
    display: flex;
    justify-content: space-between;
    font-size: 0.7vw;
    .left-bottom-time {
      opacity: 0.6;
      position: relative;
      margin-left: 2vw;
    }
    .left-bottom-look {
      transition: 0.25s;
      opacity: 0.6;
      &:hover {
        transform: translateY(-0.25em);
      }
    }
  }
  .left-right {
    overflow: hidden;
    margin-left: 1vw;
    width: 19vw;
    height: auto;

    .el-image {
      width: 100%;
      border-radius: 0px 10px 10px 0px;
      height: 100%;
      transition: transform 1s ease; /* 过渡效果 */
    }
  }
}
</style>