<template>
  <div id="back">
    <div class="header" ref="indexHeader">
      <div>
        <img src="@/assets/competition/back.jpg" alt="" />
      </div>
      <div class="title">
        <span @click="Refresh()">软件竞赛</span>
      </div>
      <div class="header-bottom">
        <div class="time">快来参加每月积分赛，提升你的排名吧</div>
        <div class="look" title="您的ip地址">{{ ip }}</div>
      </div>
    </div>
    <div class="center">
      <div class="left">
        <el-card class="introduce" shadow>
          <div class="competition-title">竞赛</div>
          <div class="competition-text">
            编程竞赛是一项旨在考察参与者计算机编程和算法设计能力的比赛，通过解决一系列挑战性问题，展示编程技能。
          </div>
          <div>
            <div class="competition-target">目标</div>
            <div class="competition-text">
              培养参与者的问题解决能力、算法思维，促使其在竞争中不断提升编码水平。
            </div>
          </div>
          <div>
            <div class="competition-type">类型</div>
            <div class="competition-text">
              算法设计：涉及数据结构和算法的编程问题。
            </div>
            <div class="competition-text">
              编码实践：要求高效编写特定功能或解决实际问题的代码。
            </div>
          </div>
          <div>
            <div class="competition-technology">技术</div>
            <div class="competition-text">竞赛期间可使用指定技术支持渠道。</div>
            <div class="competition-text">请勿在竞赛中寻求外部协助。</div>
          </div>
          <div>
            <div class="competition-prize">奖品</div>
            <div class="competition-text">丰厚奖金。</div>
            <div class="competition-text">荣誉证书。</div>
          </div>
          <div>
            <div class="competition-result">结尾</div>
            <div class="competition-text">
              愿各位参与者在竞赛中斩获佳绩，挑战自我，共同促进编程技能的提升！
            </div>
          </div>
        </el-card>
        <el-card class="left-bottom-card">
          <div style="display: flex">
            <div class="left-bottom-title">
              排名<span style="font-size: 1vw">(积分赛)</span>
            </div>
            <img src="@/assets/competition/map.png" alt="图片加载失败" />
          </div>

          <div v-if="rankGame.length != 0">
            <div
              class="bottom-rank"
              v-for="(item, index) in rankGame"
              :key="index"
            >
              <span :class="index < 3 ? 'good' : ''">{{ index + 1 }}</span>
              {{ item.major }}{{ item.year }}{{ item.username }}
              <span>{{ item.score }}</span>
            </div>
          </div>
          <div v-else style="text-align: center; padding: 3vh">暂无排名</div>
        </el-card>
        <el-card class="right-bottom-card">
          <div class="right-bottom-title">
            <span>往届竞赛回顾</span>
            <span class="cooperation" @click="CompetitionCooperation"
              >竞赛合作</span
            >
          </div>
          <div v-if="lastGame.length != 0">
            <el-descriptions
              :colon="false"
              style="margin-top: 2vh; font-size: 1vw"
            >
              <el-descriptions-item label="以往竞赛"></el-descriptions-item>
              <el-descriptions-item label="时间"></el-descriptions-item>
              <el-descriptions-item label="状态"></el-descriptions-item>
            </el-descriptions>
            <el-descriptions
              :colon="false"
              v-for="(item, index) in 1"
              :key="index"
              style="margin-top: 2vh; font-size: 1vw"
            >
              <el-descriptions-item label="软件竞赛"></el-descriptions-item>
              <el-descriptions-item label="2024-4-30 "></el-descriptions-item>
              <el-descriptions-item label="已开启"></el-descriptions-item>
            </el-descriptions>
          </div>
          <div v-else style="text-align: center; padding: 3vh">暂无排名</div>
        </el-card>
      </div>
      <div class="right">
        <el-card shadow>
          <div class="right-prize">奖品</div>
          <div class="right-point-race" @click="ToPointRace">积分赛</div>
          <div class="right-apply">报名</div>
          <div class="right-apply" @click="ToCompetitionTopic()">样题</div>
        </el-card>
        <div>
          <IndexAboutMeVue></IndexAboutMeVue>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import IndexAboutMeVue from '../index/IndexAboutMe.vue';
export default {
  components: {IndexAboutMeVue},
  data() {
    return {
      ip: "", //用户ip地址
      rankGame: [], //排名数据
      lastGame: [1], //竞赛回顾
    };
  },
  mounted() {
    //获取用户的ip地址渲染
    this.GetUserIp();
    //初始化页面参数内容
    this.BlogDetailStart();
    //获取积分赛排名渲染数据
    this.GetCompetitionRanking();
  },
  beforeDestroy() {
    //移除监听事件
  },
  methods: {
    //跳转积分赛
    ToPointRace() {
      this.$router.replace("/point-race");
    },
    //进入竞赛合作页面
    CompetitionCooperation() {
      let id = localStorage.getItem("id");
      this.$axios.get(`/question/IfQuestionAdmin?userId=${id}`).then((res) => {
        if (res.data.code == 200) {
          this.$router.push("/question/管理");
        } else {
          let password = prompt("请输入密钥");
          this.$axios
            .get(`/question/AddQuestionAdmin?key=${password}&userId=${id}`)
            .then((res) => {
              if (res.data.code == 200) {
                this.$router.push("/question/管理");
              } else {
                this.$notify.error({ message: "密钥错误" });
              }
            });
        }
      });
    },
    //获取积分赛排名渲染数据
    GetCompetitionRanking() {
      this.$axios.get(`/question/GetCompetitionRanking`).then((res) => {
        this.rankGame = res.data.data;
      });
    },
    //前往样题页面
    ToCompetitionTopic() {
      this.$router.push("/topic");
    },
    //初始化页面参数内容
    BlogDetailStart() {
      //设置背景色为透明色
      this.$bus.$emit("SetIndexTopBackgroundColor", 0);
      //到这个页面到顶部状态
      window.scrollTo({ top: 0, behavior: "auto" });
    },
    //刷新页面
    Refresh() {
      location.reload();
    },
    //发起请求获取ip地址渲染
    GetUserIp() {
      this.$axios.get("/question/GetUserIp").then((res) => {
        this.ip = res.data.data;
      });
    },
  },
};
</script>

<style lang="less" scoped>
#back {
  font-size: 1vw;
  color: white;
  height: auto;
}
.header {
  width: 100%;
  height: 55vh;
  position: relative;
  z-index: 0;
  overflow: hidden;
  img {
    width: 100vw;
    height: 55vh;
    filter: brightness(0.3);
    position: absolute;
    z-index: -1;
  }
  .title {
    text-align: center;
    font-size: 2vw;
    margin-top: 20vh;
    span {
      position: relative;
      cursor: pointer;
      padding: 0.1vw;
    }
  }
  .title span::after {
    display: block;
    width: 0;
    height: 1px;
    background-color: white;
    content: "";
    position: absolute;
    bottom: 0;
    right: 0%;
    transition: 1s;
  }
  .title span:hover::after {
    left: 0;
    width: 100%;
  }
  .header-bottom {
    display: flex;
    justify-content: center;
    margin-top: 10vh;
    .look {
      margin-left: 10vh;
    }
  }
}
.center {
  display: flex;
  justify-content: center;
  padding-bottom: 5vh;
  .left,
  .right {
    align-self: flex-start;
    margin-top: 4vh;
  }
  .left {
    width: 55vw;
    .introduce {
      .competition-title {
        padding: 1vh;
        font-size: 2vw;
        font-weight: 600;
      }
      .competition-text {
        line-height: 4vh;
        padding: 2vh;
        text-indent: 2em;
      }
      .competition-target,
      .competition-type,
      .competition-technology,
      .competition-prize,
      .competition-result {
        font-size: 1.5vw;
        padding: 2vh;
        font-weight: 600;
      }
    }
    .left-bottom-card {
      margin-top: 2vh;
      img {
        width: 3vw;
        height: 2vw;
      }
      .left-bottom-title {
        font-weight: 600;
        font-size: 3vh;
        margin-bottom: 1vh;
      }
    }
    .right-bottom-card {
      border: none;
      margin-top: 3vh;
      position: relative;
      .right-bottom-title {
        font-size: 3vh;
        display: flex;
        font-weight: 600;
        justify-content: space-between;
        color: #595959;
        .cooperation {
          font-size: 1.5vh;
          color: blue;
          margin-top: 1.5vh;
          cursor: pointer;
        }
      }
      &:hover {
        box-shadow: 4px 4px 10px #c5c5c5;
      }
    }
    .bottom-rank {
      padding-top: 2vh;
      border-bottom: 1px solid rgba(0, 0, 0, 0.121);
      border-radius: 1vh;
      justify-content: space-around;
      display: flex;
      padding: 3vh;
      .good {
        color: red;
      }
      &:last-child {
        border-bottom: none;
      }
    }
  }
  .right {
    width: 20vw;
    position: sticky;
    top: 2vh;
    margin-left: 1vw;
    .el-card {
      border-radius: 5px;
      .right-apply,
      .right-point-race,
      .right-prize {
        display: flex;
        justify-content: center;
        padding: 2vh;
        cursor: pointer;
      }
    }
  }
}
</style>