<template>
  <div>
    <div class="" v-if="shares.length != 0">
      <div class="item" v-for="(item, index) in shares" :key="index">
        <el-card>
          <div class="title" @click="OnopenNewHref(item.content)">
            网址：{{ item.content }}
          </div>
          <div class="content">详情：{{ item.title }}</div>
          <div class="love">
            <i class="el-icon-magic-stick"> {{ item.love }}</i>

            <el-popconfirm
              title="是否删除？"
              @confirm="DeleteShareUrlCollector(item.uuid)"
            >
              <i class="el-icon-delete" slot="reference"> </i>
            </el-popconfirm>
          </div>
        </el-card>
      </div>
      <el-pagination
        background=""
        size="small"
        :total="total"
        @current-change="handeChange"
      ></el-pagination>
    </div>
    <div class="item-else" v-else>
      <el-card> 没有发表网址呢 </el-card>
    </div>
    <el-dialog :visible.sync="visible" title="添加网址">
      <el-form label-width="auto">
        <el-form-item label="请输入网址：">
          <el-input v-model="form.content" style="80%">
            <el-button @click="clickCopy()" slot="append">粘贴</el-button>
          </el-input>
        </el-form-item>
        <el-form-item label="请输入网址详情：">
          <el-input
            v-model="form.title"
            @input="sensitiveWord()"
            type="textarea"
            rows="10"
            show-limit-word
            maxlength="200"
          >
          </el-input>
        </el-form-item>
        <el-form-item label="分类：">
          <el-select v-model="form.classify">
            <el-option value="前端"></el-option>
            <el-option value="后端"></el-option>
            <el-option value="算法"></el-option>
            <el-option value="其它"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div style="display: flex; justify-content: space-around">
        <el-button @click="Reset()">重置</el-button>
        <el-button @click="AddShareUrl()" type="primary">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      id: localStorage.getItem("id"), //获取用户的个人id
      shares: [], //存放自己的数组数据
      skip: 1, //处理用户收藏翻页数据
      total: 0, //处理用户收藏总条数
      form: {
        title: "",
        content: "",
        classify: "",
        id: localStorage.getItem("id"),
      }, //表单数据
      visible: false, //是否展示发表网址
    };
  },
  mounted() {
    //获取用户收藏的网址
    this.MyShareUrlCollector();
    //监听是否打开留言页面
    this.$bus.$on("ShowPublishLeaveWords", () => {
      this.visible = true;
    });
  },
  methods: {
    //用户点击确定发布网址
    AddShareUrl() {
      if (
        this.form.title == "" ||
        this.form.content == "" ||
        this.form.classify == ""
      ) {
        this.$notify.error({ message: "Please input" });
        return;
      }
      const reg1 = /^https?:\/\//gi;
      const reg2 = /^www\./gi;
      if (!reg1.test(this.form.content) && !reg2.test(this.form.content)) {
        this.$notify.error({ message: "Please enter the correct URL" });
        return;
      }
      this.$axios.post("/ShareUrl/AddShareUrl", this.form).then((res) => {
        if (res.data.code == 200) {
          this.visible = false;
          this.Reset();
          this.$notify.success({ message: "Release success" });
        }
      });
    },
    //点击粘贴用户剪切板内容
    async clickCopy() {
      //获取权限
      if (navigator) {
        this.form.content = await navigator.clipboard.readText();
      } else {
        this.$notify.error({ message: "The clipboard is forbidden to open" });
      }
    },
    //敏感词过滤
    sensitiveWord() {
      const reg =
        /他妈|你妈|操|傻逼|鸡巴|JB|脑残|恶心|废物|有病|无耻|废物|妈的|智障|死妈|傻鸟|操你妈|去死/gi;
      if (reg.test(this.form.title)) {
        this.form.title = this.form.title.replace(reg, "*");
      }
    },
    //重置内容
    Reset() {
      this.form.title = "";
      this.form.content = "";
      this.form.classify = "";
    },
    //处理用户翻页
    handeChange(skip) {
      this.skip = skip;
      this.MyShareUrlCollector();
    },
    //处理用户删除网址
    DeleteShareUrlCollector(e) {
      this.$axios.get(`/ShareUrl/DeleteShareUrl?uuid=${e}`).then((res) => {
        if (res.data.code == 200) {
          this.$notify.success({ message: "Successfully delete" });
          this.MyShareUrlCollector();
        }
      });
    },
    //打开新网站
    OnopenNewHref(e) {
      window.open(e);
    },
    //获取用户所收藏的网址
    MyShareUrlCollector() {
      this.$axios
        .get(`/ShareUrl/GetMyUserShareUrl?id=${this.id}&skip=${this.skip}`)
        .then((res) => {
          if (res.data.code == 200) {
            this.total = res.data.data.total;
            this.shares = res.data.data.records;
          }
        });
    },
  },
};
</script>

<style lang="less" scoped>
.item {
  padding: 3vh;
  .el-card {
    .title {
      padding-top: 3vh;
      cursor: pointer;
    }
    .content {
      padding-top: 3vh;
    }
    .love {
      padding-top: 3vh;
      display: flex;
      justify-content: flex-end;
      .el-icon-magic-stick {
        color: red;
        cursor: pointer;
      }
      .el-icon-delete {
        cursor: pointer;
        margin-left: 1vw;
      }
    }
  }
}
.item-else {
  text-align: center;
  color: black;
}
</style>